@value menuHeight: 225px;

.mutliselect {
	composes: input from '../../../common/input.module.css';

	display: flex;
	align-items: center;
	padding: 0 8px 0 14px !important;
	position: relative;
	background-color: #fff;
}

.mutliselect.mutliselectActive {
	border-bottom-color: transparent !important;
	border-bottom-left-radius: 0 !important;
	border-bottom-right-radius: 0 !important;
	border-color: #00d2fa !important;
}

.mutliselectDisabled {
	composes: inputDisabled from '../../../common/input.module.css';
}

.mutliselectSearch {
	width: 100% !important;
	height: 46px !important;
	padding: 18px 0 0 !important;
	margin: 0 !important;
	border: 0 !important;
	background-color: transparent !important;
	outline: none;
}

.mutliselectMenu {
	position: absolute;
	z-index: 10;
	background-color: #fff;
	top: 100%;
	left: -2px;
	right: -2px;
	display: none;
	border-radius: 6px;
	border: 2px solid #00d2fa;
	max-height: menuHeight;

	border-top: 0;
	border-top-left-radius: 0;
	border-top-right-radius: 0;
}

.mutliselectActive .mutliselectMenu {
	display: block;
}

.mutliselectMenuHeader {
	padding: 6px 16px;
}

.selectAllBtn {
	font-size: 14px;
	font-weight: 600;
	line-height: 1.36;
	color: #536476;
	outline: none;
	padding: 0;
	border: 0;
	width: 100%;
	text-align: left;
	cursor: pointer;
	text-transform: uppercase;
	background-color: #fff;
}

.selectAllBtn:disabled {
	color: #d7e0e7;
	opacity: 1 !important;
}

.mutliselectMenuBody {
	max-height: calc(menuHeight - 31px - 6px);
	overflow-y: scroll;
	overflow-x: hidden;
	padding-bottom: 12px;
	margin-bottom: 6px;
}

.multiselectMenuItem {
	width: 100%;
	padding: 0 16px;
	display: flex;
	align-items: center;
	cursor: pointer;
	padding-left: 1.8em;
}

.multiselectMenuItem svg {
	flex: none;
}

.multiselectMenuItem span {
	word-break: break-word;
}

.sideBtn {
	border: 0;
	outline: none;
	background: transparent;
	cursor: pointer;
	padding: 0;
	height: 24px;
}

.clearBtn {
	composes: sideBtn;
	float: right;
}

.toggleBtn {
	composes: sideBtn;
	transition: transform 0.2s ease-in;
	float: right;
}

.toggleBtnExpanded {
	transform: rotate(180deg);
}

.multiselectLoader {
	display: flex !important;
	flex-flow: column;
	justify-content: center;
	height: 100%;
}

.multiselectLoader > :global(.icon) {
	margin: 0;
}
