.spotPricingRateContainer {
	background-color: rgba(0, 0, 0, 0.05);
	text-align: center;
	border-radius: 5px;
	padding: 5% 0;
}

.spotPricingRateContainer label {
	font-weight: 600;
	font-size: large;
	color: #80c17d;
	text-transform: uppercase;
}

.spotPricingRateContainer h3 {
	margin-top: 0;
}

.error {
	margin-top: 10px !important;
	margin-bottom: 10px !important;
	color: red;
}

.clear {
	top: 32%;
	left: 10px;
	color: #8bc68a;
	cursor: pointer;
	font-size: 17px;
	font-weight: 600;
}

.floatRight {
	float: right;
}

.cargoItemsContainer {
	padding-top: 1rem;
	padding-bottom: 1rem;
}
