@value colors: './../../../common/common.module.css';
@value primaryColor from colors;

.hidden {
	display: none;
}
.tab:global(.ui.pointing.secondary.menu) > :global(a.item) {
	padding: 8px;
	color: #6d7c8b;
	border-width: 4px;
	border-color: transparent;
}
.tab:global(.ui.pointing.secondary.menu) > :global(a.active.item),
.tab:global(.ui.pointing.secondary.menu) > :global(a.active.item):hover {
	color: primaryColor;
	border-color: primaryColor;
	border-width: 4px;
}

.alertIcon {
	composes: alertIcon from '../elements/CoreIcon/CoreIcon.module.css';
}

.fullWidthMenuItems {
	border-bottom: 1px solid rgba(0, 0, 0, 0.1) !important;
	padding-bottom: 1px;
}
.fullWidthMenuItems > :global(.item) {
	flex-grow: 2 !important;
	justify-content: center;
}
