@value colors: './../../../../common/common.module.css';
@value primaryColor, labelsColor from colors;

.loadNumber {
	color: primaryColor !important;
	width: 80px;
	padding-right: 10px;
}

.loadNumber:hover {
	color: primaryColor !important;
	text-decoration: underline;
}

.customerNameLink {
	color: primaryColor !important;
	width: 80px;
	padding-right: 10px;
}

.customerNameLink:hover {
	color: primaryColor !important;
	text-decoration: underline;
}

.highlightCustomer {
	min-width: 200px;
	color: #023a52 !important;
}

.carrierColumn {
	min-width: 200px;
}

.btnsWrapper {
	display: flex;
	justify-content: flex-end;
	margin: 20px 0;
}

.cancelButton {
	composes: tertiaryButton from '../../../../core/components/input/CoreButton/CoreButton.module.css';
	margin-right: 20px !important;
	cursor: pointer;
}
.saveButton {
	composes: primaryButton from '../../../../core/components/input/CoreButton/CoreButton.module.css';
	cursor: pointer;
	margin-right: 0 !important;
}
.tagList {
	min-width: 120px;
}
