.baseTile {
	width: 100% !important;
	top: 0;
	bottom: 0;
	height: 33.3em !important;
}

.baseTileOverflow {
	overflow-y: scroll;
	overflow-x: hidden;
}

.smallTile {
	max-height: 12em !important;
}
.wideTile {
	width: 300% !important;
	top: 0;
	bottom: 0;
	height: 28.5em !important;
}
.mediumTile {
	width: 100% !important;
	top: 0;
	bottom: 0;
	height: 22em !important;
}
.longTile {
	height: 34em !important;
	margin-top: 10px;
}
