.labels {
	padding: 0 0.5em;
	vertical-align: bottom;
	top: 0.1em;
}
.labelsHidden {
	composes: labels;
	opacity: 0;
	display: none;
}
.label {
	white-space: nowrap;
	text-overflow: ellipsis;
	max-width: 250px;
	overflow: hidden;
	padding: 0.4em 0.8em !important;
}
.title {
	cursor: pointer;
	font-size: 16px;
	color: #536476;
	padding: 2px 5px 3px 40px;
	display: inline-block;
	white-space: nowrap;
	line-height: 40px;
	position: relative;
	min-width: 8rem;
	width: 100%;
}

.header {
	display: flex;
	align-items: center;
}

.content {
	padding: 0 42px 21px;
}

.slimContent {
	padding-top: 0;
	padding-left: 5%;
	padding-right: 5%;
	padding-bottom: 3px;
}

.validationIcon {
	float: right;
	padding-top: 0.2em;
}
.validationIconHidden {
	composes: validationIcon;
	opacity: 0;
	display: none;
}
.deleteButton {
	float: right !important;
}

@media only screen and (max-width: 850px) {
	.labels {
		display: none;
	}
}

.container {
	padding: 0 !important;
	box-shadow: none !important;
	border: solid 2px #dde4eb !important;
}

.asideActions {
	width: 100px;
	align-items: center;
	margin-left: auto;
	margin-right: 5px;
}

.arrowIcon {
	position: absolute;
	left: 17px;
	top: 16px;
	transition: transform 0.2s ease-in;
}

.arrowIconActive.arrowIcon {
	transform: rotate(90deg);
}
