@value primaryColor: #80C17D;
@value labelsColor: #536476;

.transition {
	transition: all 250ms ease-in-out;
}

body ::-webkit-scrollbar {
	width: 5px;
	background: transparent;
}

.blockUp {
	z-index: 1;
}

.noRightPadding {
	padding-right: 0 !important;
}
.noRightPadding > div > div > div + div {
	text-align: right;
}

.topIntention {
	margin-top: 15px !important;
}

.commonButtonHeight {
	height: 50px;
}

.flexDisplay {
	display: flex;
	justify-content: center;
	align-items: center;
}

.fullHeight {
	height: 100%;
}
/* Helper class to make column take more width and allow it to break text to other line.
   Can be found in DocumentMasterDetail, Payables, etc.
*/
.wordBreakColumn {
	word-break: break-all;
	max-width: none !important;
}

.updateTrackTraceButton {
	right: 0;
}

.updateCheckCall {
	composes: updateTrackTraceButton;
	top: 17px;
}

.updateDispatch {
	composes: updateTrackTraceButton;
	top: 17px;
}

.updateDispatchInGrid {
	composes: updateTrackTraceButton;
	top: 13px;
}

.buttonInTabs {
	left: 5px;
}
