.alertIcon {
	content: '';
	display: block;
	border-radius: 12px;
	width: 12px;
	height: 12px;
	background-color: #fa5680;
	float: left;
	top: 0.3em;
}

.messageContainer {
	cursor: pointer;
	width: 98%;
	display: table;
}

.alertMessage {
	margin-left: 20px;
}

.unReadMessage {
	font-weight: bold;
}

.ellipsisButton {
	width: 30px;
	display: none;
	vertical-align: middle;
}

.messageContainer:hover > .ellipsisButton {
	display: table-cell;
}

.creationDate {
	font-weight: normal;
	font-style: italic;
	font-size: small;
}
