.textAlignCenter {
	text-align: center;
}

.spotPricingRateContainer {
	background-color: rgba(0, 0, 0, 0.05);
	border-radius: 5px;
	padding: 7%;
	display: inline-block;
	/*
	 * min-width helps assure the boxes are usually the same size,
	 * at least until we get to very large rates (~9k+), at which point,
	 * a bit of a size discrepancy is ok because those rates aren't
	 * expected to happen much, if ever
	 * 26 was chosen as it is 2x the length of the longest label text
	 * (using just 13 wasn't enough, due to font sizing I think)
	 * source: https://stackoverflow.com/a/16586438/5038805
	 */
	min-width: 26ch;
}

.textBold {
	font-weight: 600;
}

.textUppercase {
	text-transform: uppercase;
}

.fontSizeLarge {
	font-size: 1.3rem;
}

.fontSizeLarger {
	font-size: 2.2rem !important;
	line-height: 3rem !important;
}

.textGreen {
	color: #80c17d !important;
}

.textGray {
	color: #536476 !important;
}

.spotPricingRateContainer h2 {
	margin-top: 0;
}
