.inputContainer {
	min-height: 46px !important;
}

.textAreaContainer {
	min-height: 8.3em !important;
}

.input {
	composes: input from '../../../../common/input.module.css';
}

.inputFocused {
	composes: inputFocused from '../../../../common/input.module.css';
}

.inputDisabled {
	composes: inputDisabled from '../../../../common/input.module.css';
}

.inputError {
	composes: inputError from '../../../../common/input.module.css';
}

.inputFilled {
	composes: inputFilled from '../../../../common/input.module.css';
}

.redText {
	color: red !important;
}

.redText:disabled {
	color: #f49c9e !important;
}

.label {
	composes: inputLabel from '../../../../common/input.module.css';
}

.transparent {
	background-color: transparent;
}

.label:after {
	color: #6d7c8b !important;
}

.activeLabel {
	composes: inputLabelActive from '../../../../common/input.module.css';
}

.activeLabel:after {
	color: #6d7c8b !important;
}

.disabledLabel,
.disabledLabel:after {
	color: rgba(17, 14, 14, 0.6) !important;
}

/* Hide the spinner */
.input[type='number']::-webkit-inner-spin-button,
.input[type='number']::-webkit-outer-spin-button,
.input[type='date']::-webkit-inner-spin-button,
.input[type='date']::-webkit-outer-spin-button {
	appearance: none;
	margin: 0;
}

/* Hide the clear, "x", button */
.input[type='date']::-webkit-clear-button {
	display: none;
	appearance: none;
}

.resizeDisable {
	resize: none !important;
}

.addon {
	font-size: 16px;
	font-weight: normal;
	font-stretch: normal;
	font-style: normal;
	line-height: 1.19;
	letter-spacing: normal;
	color: #6d7c8b;
	border-top-right-radius: 0 !important;
	border-bottom-right-radius: 0 !important;
}

.addon:hover {
	border: 2px solid #a9b2bb !important;
}

.rightIconInput {
	border-bottom-right-radius: 0 !important;
	border-top-right-radius: 0 !important;
}

@media (max-width: 1500px) {
	.datePickerInputContent {
		font-size: 14px !important;
	}
}

.datePickerInputContent::-webkit-calendar-picker-indicator {
	color: transparent;
	position: absolute;
	right: 0;
	top: 50%;
	transform: translateY(-50%);
}

.datePickerInputContent.calendarHidden::-webkit-calendar-picker-indicator {
	display: none;
}

.textAreaLabel {
	background: transparent;
	width: calc(100% - 9px);
	left: 2px;
	top: 11px;
	padding-left: 14px;
	line-height: 18px !important;
	opacity: 1;
	border-top-left-radius: 6px;
}
.activeLabel.textAreaLabel {
	color: rgba(109, 124, 139, 0.6) !important;
	background: #fff;
}
.disabledLabel.textAreaLabel {
	background: rgb(249, 249, 251);
}
