.inputContainer {
	min-height: 50px !important;
	padding-right: 1.7em !important;
	composes: input from '../../../common/input.module.css';
}

.multipleLabels {
	padding-top: 1.2em !important;
}
.multipleLabels > :global(input.search) {
	padding-top: 0.5em !important;
}

.singleLabel {
	padding-bottom: 0 !important;
}

.inputContainerError {
	composes: inputContainer;
	composes: inputError from '../../../common/input.module.css';
}

.inputFocused {
	composes: inputFocused from '../../../common/input.module.css';
}

.inputFilled {
	composes: inputFilled from '../../../common/input.module.css';
}

.inputContainer > :global(input) {
	padding: 18px 14px 0px 14px !important;
	font-size: 16px !important;
	line-height: 1.19 !important;
	color: #6d7c8b !important;
	height: 100% !important;
}

.inputContainer > :global(.text) {
	left: 0 !important;
	padding: 6px 0 0 !important;
	font-size: 16px !important;
	line-height: 1.19 !important;
	max-width: 100% !important;
	height: 100%;
	overflow: hidden !important;
	white-space: nowrap !important;
	text-overflow: ellipsis !important;
	margin: 0 !important;
}

.inputContainer span {
	color: #536476 !important;
}

.inputContainer > :global(.icon) {
	float: right;
	padding: 16px 14px 0 0 !important;
}
.inputContainer:global(.visible) ~ .activeLabel {
	z-index: 11 !important;
	transition: 2ms;
}

.label {
	composes: inputLabel from '../../../common/input.module.css';
}

.label:after {
	color: #6d7c8b !important;
}

.activeLabel {
	composes: inputLabelActive from '../../../common/input.module.css';
}

.activeLabel:after {
	color: #909090 !important;
}

.disabled {
	composes: inputDisabled from '../../../common/input.module.css';
}

.disabledLabel,
.disabledLabel:after {
	color: rgba(144, 144, 144, 0.6) !important;
}

.inputContainer:global(.ui.dropdown:not(.loading)
		> .dropdown.icon:not(.clear):before) {
	content: url(./assets/down-chevron.svg) !important;
}

.inputContainer:global(.ui.dropdown:not(.loading)
		> .dropdown.icon.clear:before) {
	content: url(./assets/close.svg) !important;
}

.inputContainer:global(.ui.loading.selection.dropdown > i.icon) {
	height: 100% !important;
}

.inputContainer:global(.ui.dropdown > .dropdown.icon) {
	padding: 0 !important;
	height: 100% !important;
	display: flex;
	align-items: center;
	padding: 0 4px !important;
}

.inputContainer:global(.ui.dropdown.active > .dropdown.icon:not(.clear)) {
	transition: transform ease-in 0.2s;
	transform: rotate(180deg);
}

.inputContainer:global(.ui.selection.dropdown .menu) {
	min-width: calc(100% + 4px);
}
.inputContainer:global(.ui.selection.active.dropdown .menu) {
	border: solid 2px #00d2fa !important;
	border-top: none !important;
	left: -1px !important;
	box-shadow: none !important;
}

.inputContainer:global(.ui.selection.dropdown .menu > .item) {
	border-top: none;
}

.inputContainer:global(.ui.selection.active.dropdown
		.menu)::-webkit-scrollbar-track {
	background-color: transparent;
}

.inputContainer:global(.ui.selection.active.dropdown
		.menu)::-webkit-scrollbar-thumb {
	background-color: #7e92a3;
}

.moveBottom:global(.ui.bottom.pointing.dropdown > .menu) {
	top: 47px !important;
	bottom: unset !important;
}

.moveBottom:global(.ui.upward.selection.dropdown.visible) {
	border-top-left-radius: 6px !important;
	border-top-right-radius: 6px !important;
	border-bottom-left-radius: 0 !important;
	border-bottom-right-radius: 0 !important;
}

.moveBottom:global(.ui.active.selection.dropdown) {
	border-bottom: none !important;
}

.moveBottom:global(.ui.selection.active.dropdown .menu) {
	border-top-left-radius: 0 !important;
	border-top-right-radius: 0 !important;
	left: -2px !important;
}

.moveBottom:global(.ui.selection.dropdown.active),
.moveBottom:global(.ui.selection.dropdown.visible) {
	border: solid 2px #00d2fa !important;
	border-bottom: none !important;
}

.moveBottom:global(.ui.bottom.pointing.dropdown > .menu):after {
	display: none;
}

/** Combined fields **/

.inputsContent {
	display: inline-flex;
	justify-content: center;
	width: 100% !important;
}

/** input **/
.inputsContent > div > div {
	width: 100% !important;
}

.inputArea {
	width: 100% !important;
}

/** dropdown **/
.inputsContent > div + div > div {
	width: 62px !important;
	border-top-left-radius: 0 !important;
	border-bottom-left-radius: 0 !important;
	background-color: #dde4eb !important;
	border: none !important;
}

.inputsContent
	> div
	+ div
	> .moveBottom:global(.ui.selection.dropdown.visible) {
	border-top-left-radius: 0 !important;
	border-bottom-left-radius: 0 !important;
}

.combinedDropdown + label {
	display: none !important;
}

.combinedDropdown > input {
	top: -6px !important;
}

.combinedDropdown:global(.ui.selection.active.dropdown .menu) {
	border-top-left-radius: 0 !important;
	border-top-right-radius: 0 !important;
	box-shadow: 0 4px 13px 0 rgba(0, 0, 0, 0.15) !important;
	border: solid 2px #00d2fa !important;
	border-top: none !important;
}

.combinedDropdown:global(.ui.search.dropdown > .text) {
	position: absolute !important;
	top: 10px;
	left: 6px !important;
	min-width: 72px !important;
	font-size: 16px !important;
	font-weight: normal;
	font-stretch: normal;
	font-style: normal;
	line-height: 1.19;
	letter-spacing: normal;
	color: #6d7c8b !important;
	text-shadow: 0 0 black;
}
