.toggleContainer {
	composes: transition from '../../../../common/common.module.css';
	width: 100%;
	height: 60px;
	align-content: center;
	border-bottom: 1px solid rgb(189, 198, 205, 0.1);
	cursor: pointer;
	color: #bdc6cd;
	display: inline-block;
}

.navBarIcon {
	composes: transition from '../../../../common/common.module.css';
	color: inherit;
	position: absolute;
	top: 33%;
	left: 17px;
}

.navBarIconExpanded {
	composes: navBarIcon;
	left: 160px;
}

.toggleContainer:hover {
	color: white;
}

.logo {
	composes: transition from '../../../../common/common.module.css';
	top: 2px;
	width: 150px;
}

.logoHidden {
	pointer-events: none;
	composes: logo;
	opacity: 0;
}
