.dividerContainer {
	margin-top: 15px !important;
	margin-bottom: 15px !important;
	text-align: center;
	font-weight: bold;
}

.totalContainer {
	margin-top: 15px !important;
	font-weight: bold;
}

.buttonContainer {
	float: right;
}

.container {
	display: inline-block;
	margin-left: 25%;
	width: 9rem;
	min-height: 100px;
	border-radius: 10px;
	padding: 1.2rem 0.5rem;
}

.container :global(p) {
	display: table;
	margin: 0 !important;
	padding: 0.3rem;
	padding-top: 1.4rem;
}

.header {
	font-size: 1rem;
	font-weight: bold;
	padding: 0.1rem 0.3rem;
}

.count {
	line-height: 1rem;
	font-size: 3rem;
	font-weight: bold;
	opacity: 0.75;
}
