.dateInputClearBtn {
	position: absolute;
	z-index: 2;
	right: 3px;
	top: 13px;
	padding: 0 !important;
	background-color: transparent !important;
	border: none;
	opacity: 0.8;
	cursor: pointer;
}
.dateInputClearBtn:hover,
.dateInputClearBtn:focus {
	opacity: 1;
}
.dateInputClearBtn:focus {
	outline: 1px dotted #777;
}
