@value colors: './../../../common/common.module.css';
@value primaryColor, labelsColor from colors;

.buttons {
	display: block;
	padding-top: 10px;
}

.selectableRow {
	cursor: pointer;
}

/* --- Since I need to override hover effect on selectable tr, completely through all the site  --- */
:global(.ui.selectable.table tbody tr:hover),
:global(.ui.table tbody tr td.selectable:hover) {
	background-color: #f3f3f3 !important;
	color: #467599 !important;
}

.centered {
	position: relative;
	top: 50%;
	transform: translateY(-50%);
}

.floatedRight {
	float: right !important;
}

.selectHeader {
	float: left;
}

.actionButton {
	padding: 0px;
}

.right {
	text-align: right !important;
}

.deleteButton {
	composes: deleteConfirmationButton from '../input/CoreActionButton/DeleteConfirmationButton.module.css';
}

.settingIcon i {
	display: none !important;
}

.settingIcon:before {
	content: url('./assets/settings.svg');
}

.settingsBlock {
	position: relative;
	top: 13px;
	margin-left: 13px;
}

.settingHelpVideoIcon {
	bottom: 8px;
}
.detailPadding {
	padding: 0.5em !important;
}

tr.tablesHelperBlock {
	z-index: 1 !important;
	background-color: #f3f3f3;
}

tr.tablesHelperBlock td {
	padding: 0 !important;
}

.chevronMenu {
	right: 35px;
	bottom: 11px;
}

.chevronMenu > div + svg + div {
	opacity: 0;
}

.checkboxAlign {
	top: 3px;
}

.chevronArea {
	position: absolute;
}

.checkedAny {
	height: 3px;
	width: 14px;
	background: #becfdb;
	position: absolute;
	left: 1.3em;
	bottom: 1.1em;
	pointer-events: none;
}

.tableCellInner {
	overflow: hidden;
	text-overflow: ellipsis;
	display: -webkit-box;
	-webkit-line-clamp: 2; /* number of lines in cell to show */
	-webkit-box-orient: vertical;
}

.tableCellInner.compactTableCell {
	display: block;
}

.compactView {
	display: block;
}

.compactView :global(.ui.table td) {
	white-space: nowrap;
	padding-top: 1px;
	padding-bottom: 1px;
	height: 36px;
}

.transparentBackground {
	background-color: transparent !important;
}

.stickyHelperInner {
	position: sticky;
	left: 0;
	background-color: #f3f3f3;
	padding: 0.5em;
	z-index: 4;
}
