:global(.ui.sidebar) {
	background-color: #ecf2f6;
	height: calc(100% - 60px) !important;
}

:global(.pushable:not(body)) {
	width: 100%;
	transform: none !important;
	overflow: visible;
}

:global(.pusher) {
	overflow: unset !important;
}

:global(.pushable > .pusher.dimmed):after {
	min-height: calc(100% - 60px);
	opacity: 0 !important;
}

.sidebarContainer {
	margin-left: 20px;
	margin-right: 20px;
	margin-top: 20px;
}

.sidebarTitle {
	pointer-events: none;
	display: flex;
	background-color: #7e92a3;
	width: 100%;
	height: 70px;
	margin-bottom: 1em;
	padding-left: 20px;
	align-items: center;
}
.sidebarTitleText {
	width: 300px;
	padding-top: 10px;
	font-family: 'Source Sans Pro';
	font-size: 24px;
	font-weight: 600;
	font-stretch: normal;
	font-style: normal;
	letter-spacing: normal;
	color: #ffffff;
}

.sidebarClose {
	cursor: pointer;
}

.closeButton {
	cursor: pointer;
	position: absolute;
	right: 1em;
}

.staticState {
	position: fixed !important;
	top: 60px !important;
	width: 800px !important;
	box-shadow: -10px 10px 20px rgba(34, 36, 38, 0.15) !important;
}

@media screen and (min-width: 1920px) {
	.staticState {
		max-width: 960px;
		min-width: 480px;
	}
}

@media screen and (max-width: 1919px) and (min-width: 1440px) {
	.staticState {
		max-width: 720px;
		min-width: 360px;
	}
}

@media screen and (max-width: 1439px) and (min-width: 100px) {
	.staticState {
		max-width: 768px;
		min-width: 256px;
	}
}
