.item {
	z-index: 1;
	border-bottom-color: #536476;
	padding-top: 2px;
	padding-right: 30px;
	padding-left: 30px;
	height: 100%;
	text-align: center;
	color: #536476;
	font-family: Source Sans Pro;
	cursor: pointer;
}

.item:focus {
	outline: none;
	border-bottom-style: solid;
	border-bottom-width: 5px;
}
