.height {
	height: 100%;
	width: 100%;
}

.floatRight {
	float: right;
}

.dropColumn {
	padding-top: 8px;
	padding-left: 8px;
	padding-right: 8px;
	padding-bottom: 2px;
}

.noPadding.dropColumn {
	padding: 0;
}

.compactHeader {
	line-height: 42px;
	background-color: #d7e0e7;
	border-radius: 6px;
	width: 100%;
	display: inline-block;
	margin-bottom: 6px;
	font-size: 24px;
	padding-left: 0px;
	color: #7e92a3 !important;
}

/* Enabling 'hover' over other draggable items directly while dragging */
.draggingColumn :global(span[class*='dottedIcon']),
.draggingColumn :global(button[class*='iconButton']) {
	pointer-events: none;
}
