.iconButton {
	padding: 0.5em 0.4em !important;
	height: 30px !important;
}

.iconButton:global(.massive) {
	height: 50px !important;
}

.ui[class*='right labeled'].icon.button > .icon {
	right: 5px !important;
	border-radius: 4px;
	border-top-right-radius: inherit;
	border-bottom-right-radius: inherit;
	box-shadow: 1px 0 0 0 transparent inset;
}

.headerIconButton {
	padding: 0.5em 0.4em !important;
	font-size: 13px !important;
}

.smallIconButton {
	position: relative;
	max-height: 50px;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 50px;
	background-color: #80c17d !important;
	border-bottom-right-radius: 6px !important;
	border-top-right-radius: 6px !important;
	border-top-left-radius: 0 !important;
	border-bottom-left-radius: 0 !important;
}

.smallIconButtonPhone,
.smallIconButtonEmail {
	composes: smallIconButton;
	right: 3px;
}

.smallIconButton > :global(.icon) {
	font-size: 12.6px;
}

.linkButton {
	padding: 0 !important;
	background-color: transparent !important;
	color: #80c17d !important;
	font-weight: unset !important;
	font-family: 'Source Sans Pro', sans-serif !important;
}

.smallLinkButton {
	padding: 0 !important;
	background-color: transparent !important;
	color: #21ba45 !important;
	font-size: 10px !important;
	margin: 0 !important;
}

.linkButton:hover {
	color: #21ba45 !important;
	text-decoration: underline !important;
}

.noLinkButton {
	padding: 0 !important;
	background-color: transparent !important;
}

.arrowButton {
	margin: 0 !important;
	padding: 2px !important;
	background-color: transparent !important;
}
.arrowButton:hover,
.arrowButton:focus {
	opacity: 0.8;
}
.arrowButtonPrevious {
	transform: rotate(180deg);
}

.arrowButtonUp {
	transform: rotate(270deg);
}
.arrowButtonDown {
	transform: rotate(90deg);
}
.mainButtonsClass {
	font-weight: 600 !important;
	color: #ffffff !important;
	text-transform: uppercase !important;
	font-family: 'Source Sans Pro', sans-serif !important;
}

.primaryButton {
	composes: mainButtonsClass;
	display: inline-flex !important;
	justify-content: center;
	padding-top: 0 !important;
	padding-bottom: 0 !important;
	align-items: center;
	height: 50px;
	background-color: #80c17d !important;
}

.primaryButton:hover {
	opacity: 0.9;
}

.primaryButton:active {
	background-color: #71ad70 !important;
}

.primaryButton:disabled {
	background-color: #e3e3e7 !important;
}

.secondaryButton {
	composes: mainButtonsClass;
	display: inline-flex !important;
	align-items: center;
	height: 50px;
	font-size: 20px;
	background-color: #467599 !important;
	text-align: center !important;
}

.secondaryButton:hover {
	opacity: 0.9;
}

.secondaryButton:active {
	background-color: #3e6989 !important;
}

.secondaryButton:disabled {
	background-color: #e3e3e7 !important;
}

.tertiaryButton {
	composes: mainButtonsClass;
	display: inline-flex !important;
	align-items: center;
	height: 50px !important;
	font-size: 20px !important;
	background-color: #ffffff !important;
	color: #467599 !important;
	border: solid 2px #467599 !important;
}

.tertiaryButton:hover {
	opacity: 0.9;
	background-color: #f5f5f5;
}

.tertiaryButton:active {
	background-color: #efefef !important;
}

.tertiaryButton:disabled {
	background-color: #f5f5f5 !important;
	opacity: 35%;
}

.customIconButton {
	background: transparent !important;
	height: 30px !important;
	width: 30px !important;
}

.customIconButton:active {
	background: transparent !important;
}

.customIconButton:before {
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
	z-index: 100;
	width: 30px;
	height: 30px;
}

.edit {
	composes: customIconButton;
}

.edit:before {
	content: url('./assets/edit.svg') !important;
}
/*
hides the semantic icon when displaying an edit button. leaves the css icon we want.
*/
.edit :global(.icon) {
	display: none;
}

.edit :global(.icon) {
	/* hides the semantic icon when displaying an edit button. leaves the css icon we want.*/
	display: none;
}

.eye {
	composes: customIconButton;
}

.eye:before {
	content: url('./assets/preview.svg') !important;
}

.search {
	composes: customIconButton;
}

.search:before {
	content: url('./assets/search.svg') !important;
}

.fontLarge {
	font-size: 20px !important;
}

.noPadding {
	padding: 0 !important;
}

.smallVideoButton,
.headerVideoButton {
	height: 30px;
	padding: 0 !important;
	vertical-align: bottom !important;
	background: transparent !important;
}

.smallVideoButton svg,
.headerVideoButton svg {
	width: 30px;
	height: 30px;
}

.smallLockUnlockButton {
	width: 24px;
	height: 24px;
	padding: 1px !important;
	background-color: transparent !important;
}

.smallLockUnlockButton svg path {
	fill: #6d7c8b;
}

.headerLockUnlockButton {
	composes: headerIconButton;
	background-color: transparent !important;
}

.headerLockUnlockButton svg path {
	fill: #6d7c8b;
}

.highlightFocus {
}

.highlightFoucs:focus {
	border: 2px dotted black;
}

.flexContent {
	display: flex !important;
	justify-content: center;
	align-items: center;
}

.addButton:before {
	content: '+';
	position: relative;
	display: inline-block;
	padding: 2px 7px;
	background-color: #71ad6f;
	border-radius: 50%;
	right: 10px;
}

.addButton:focus {
	border: none !important;
}

.transparentButtonColor {
	background-color: transparent !important;
}
