@value colors: './../../../src/common/common.module.css';

.summary {
	margin: 0;
	font-size: 17px;
}

.green {
	color: #80c17d !important;
	font-weight: bold;
	padding: 0 1em 0 0;
}

.payableHeader {
	font-size: 17px;
	font-weight: bold;
}

.right {
	text-align: right;
}

.transactionAmount {
	text-align: right;
	font-size: 17px;
}

.bottomBorder {
	border-bottom: 1px solid #467599;
}

.summaryContainer {
	/* hardcoded because CoreMasterDetail has no maximum height */
	height: 335px;
	overflow: auto;
}

.payableSummary {
	float: left;
	margin: 5px 5px 5px 0;
	min-width: 110px;
}
