.textColor {
	color: #536476;
}

.floatRight {
	float: right;
}

.creditSearchGridHeight {
	height: 17em;
	overflow: auto;
}

.creditReportScroll {
	height: 19em;
	overflow-y: auto;
	overflow-x: hidden;
}

.backgroundColor {
	background-color: #ecf2f6;
	color: #536476;
}

.boldText {
	font-weight: bold;
}

.flex {
	display: inline-flex;
	width: 100%;
}

.inline {
	display: inline;
	margin-left: 10px;
}

.marginLeft {
	margin-left: auto;
	order: 2;
}

.creditreportUCClistHeight,
.creditReportArAgingListHeight {
	overflow-y: auto;
	height: auto;
	max-height: 17em;
}
.creditReportPhoneListHeight,
.creditReportOfficerListHeight {
	overflow-y: auto;
	height: auto;
	max-height: 8em;
}
.creditReportFillingListHeight,
.creditReportCustomerListHeight {
	overflow-y: auto;
	height: auto;
	max-height: 18em;
}

.addIconTextElement {
	display: inline;
	padding-left: 5px;
	font-size: 17px;
}

.addIcon {
	top: 30%;
	width: 22px;
	height: 22px;
	background-color: #80c17d;
	border-radius: 50%;
	display: inline-flex;
	justify-content: center;
	align-items: center;
	color: #fff;
	font-size: 19px;
	cursor: pointer;
}
.addIconDisable {
	composes: addIcon;
	opacity: 0.7;
	pointer-events: none;
	cursor: pointer !important;
}
.addIcon:hover {
	background-color: #72ad70;
}
