.helpButton {
	display: inline-flex;
	vertical-align: middle;
}

.headerHelpButton {
	margin: 1rem 1rem 1rem 1rem;
	display: inline-block;
	vertical-align: middle;
}

.headerHelpButton :global(.button.dropdown) {
	border-radius: 25px !important;
}

.headerHelpButton :global(.button.dropdown) :global(.icon) {
	left: 4px;
}

.popup {
	left: -8px !important;
	margin-top: 0 !important;
	border-radius: 6px !important;
	border: 1px solid #dde4eb !important;
	background-color: #f6fafd !important;
	box-shadow: 0 4px 13px 0 rgba(0, 0, 0, 0.15) !important;
	overflow: hidden;
}

.popup :global(.content) {
	padding: 0 !important;
}

.popup :global(.header) {
	display: none;
}

.iconButton {
	width: 24px;
	height: 24px;
	padding: 0 !important;
	background-color: #fff !important;
}

.iconButton :global(.info.icon),
.iconButton :global(.info.icon:before) {
	font-size: 12px;
	font-family: inherit;
	text-align: center;
	font-weight: bold;
	margin: 0 !important;
	content: 'i';
	left: 0 !important;
	line-height: 13px;
	opacity: 1;
}

.iconButton :global(.text),
.iconButton :global(.visible.menu) {
	display: none !important;
}

.dropdownItem {
	padding: 2px 15px !important;
	display: flex;
	align-items: center;
}

.dropdownItem:hover {
	background-color: #ecf2f6 !important;
}

.dropdownItem :global(.text) {
	margin-left: 8px;
	padding: 0 !important;
}

.rightMargin {
	margin-right: 0.8rem;
}

.backgroundTransparent {
	background-color: transparent !important;
}
