.headerContainer {
	composes: transition from '../../common/common.module.css';
	height: 60px;
	position: fixed;
	top: 0;
	right: 0;
	width: calc(100% - 60px);
	z-index: 10;
}

.headerContainerMenuExpanded {
	composes: headerContainer;
	width: calc(100% - 210px);
}

.contentContainer {
	composes: transition from '../../common/common.module.css';
	display: flex;
	top: 60px;
	margin-left: 60px;
	min-height: calc(100vh - 60px);
}

.contentContainerMenuExpanded {
	composes: contentContainer;
	margin-left: 210px;
}
