@value colors: './../../../../../common/common.module.css';
@value primaryColor, labelsColor from colors;

.searchToggles {
	margin-left: auto;
	display: flex;
	justify-content: flex-end;
}

.coreContainer {
	padding: 1rem;
}

.filterContainer {
	width: 11em;
	float: right;
	border: none;
	box-shadow: none;
	color: labelsColor;
	font-size: 16px;
	font-family: 'Source Sans Pro', sans-serif !important;
}

.transactionFilter {
	padding: 0.3em;
	position: relative;
	border-radius: 6px;
}

.transactionFilter span {
	padding-left: 0.4em;
}

.transactionFilter:hover {
	cursor: pointer;
	background-color: #f3f6f8;
}

.activeToggle {
	background-color: primaryColor;
	color: white;
}

.activeToggle:hover {
	background-color: primaryColor;
	color: white;
}

.filterDivider {
	display: block;
	height: 1px;
	background-color: rgba(83, 100, 118, 0.2);
	margin: 2px 0;
}

.loadFilterContainer {
	display: flex;
	align-items: flex-start;
	justify-content: flex-end;
}

.container {
	display: flex;
	flex-wrap: wrap-reverse;
}

.quickTasksContainer {
	margin-bottom: 20px;
	width: 100%;
}

@media (min-width: 1350px) {
	.quickTasksContainer {
		margin-bottom: 0;
		margin-left: 20px;
		width: auto;
	}

	.headerContainer {
		width: auto;
	}

	.container {
		flex-wrap: initial;
	}
}

.headerContainer {
	width: 80%;
}
