.header {
	font-weight: bold;
	font-size: 1.2em;
}

.address {
	font-size: 1.2em;
}

.scrollableCard {
	overflow-y: auto;
	height: 6em;
}

.entry {
	border-top: 1px solid #e3e3e3;
	padding-top: 0.4em;
}

.title {
	font-weight: bold;
	text-transform: capitalize;
}

.reference {
	font-weight: bold;
	text-transform: capitalize;
}

.padding {
	padding-left: 1em;
	padding-top: 1em;
}

.padding:first-child {
	padding-top: 0em;
}
