.TagsList {
	display: inline-flex;
	flex-wrap: wrap;
	margin: 0;
	padding: 0;
	list-style: none;
}

.TagsItem {
	margin: 4px;
	padding: 5px 10px;
	color: #202a45;
	border-radius: 4px;
	background-color: #dde4eb;
}

.TagsLabel {
	font-size: 16px;
}

.TagsPopupContent {
	border-radius: 6px !important;
	box-shadow: 0 4px 13px 0 rgba(0, 0, 0, 0.15) !important;
	border: solid 1px #dde4eb !important;
	background-color: #f6fafd !important;
}

.overFlowList {
	max-width: 800px;
	min-width: 180px;
	max-height: 250px;
	overflow: hidden;
}

.overFlowMore {
	float: right;
}
