.filtersArea {
	background: #fff;
	padding: 20px;
	border-radius: 6px;
}
.filtersTitle {
	font-size: 16px !important;
	line-height: 1.19 !important;
	color: #6d7c8b !important;
	right: 10px;
	margin-bottom: 10px;
}
.childPosition {
	display: flex;
	justify-content: center;
	align-items: center !important;
}
.addIcon {
	width: 22px;
	height: 22px;
	background-color: #80c17d;
	border-radius: 50%;
	display: flex;
	justify-content: center;
	align-items: center;
	color: #fff;
	font-size: 19px;
	cursor: pointer;
}
.addIconDisable {
	composes: addIcon;
	opacity: 0.7;
	pointer-events: none;
	cursor: pointer !important;
}
.addIcon:hover {
	background-color: #72ad70;
}
.secondaryButton {
	composes: secondaryButton from '../../../../core/components/input/CoreButton/CoreButton.module.css';
	composes: fontLarge from '../../../../core/components/input/CoreButton/CoreButton.module.css';
	padding: 0 1.5rem !important;
}
.clearButtonWrap {
	display: inline-flex;
	vertical-align: top;
}
.clearPosition {
	padding: 8px 7px !important;
}
.clearPosition i {
	margin: 0 !important;
}
.clear {
	color: #8bc68a;
	cursor: pointer;
	font-size: 17px;
	font-weight: 600;
}
.buttonContainer {
	float: right;
}
.buttonsArea {
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.saveFilterArea {
	composes: buttonsArea;
	justify-content: flex-end;
}

.savedFilterArea {
	margin: 18px 0;
	border: 1px solid #d9dadc;
}

.filterName {
	margin-bottom: 18px;
}

.favoriteFilterTitle {
	display: flex;
	justify-content: flex-end;
	color: #8bc68a;
	cursor: pointer;
	font-size: 17px;
	font-weight: 600;
	margin-bottom: 15px;
}

.backToOrdinaryTitle {
	composes: favoriteFilterTitle;
	justify-content: flex-start;
}

.favoriteItem {
	display: flex;
	justify-content: space-between;
	align-items: center;
	height: 47px;
	width: 93%;
	border-radius: 6px;
	background-color: #dbe8f1;
	font-size: 18px;
	font-weight: normal;
	font-stretch: normal;
	font-style: normal;
	line-height: 0.94;
	letter-spacing: normal;
	color: #6d7c8b;
	margin-bottom: 10px;
	padding: 0 20px;
}

.flexAlign {
	display: flex;
	align-items: center;
}

.removeFavorite {
	bottom: 5px;
	left: 10px;
}

.favoriteIconOn:after {
	content: url('../assets/fav-icon-on.svg');
	cursor: pointer;
}

.favoriteIconOff:after {
	content: url('../assets/fav-icon-off.svg');
	cursor: pointer;
}

.favoriteIconOn:hover:after {
	content: url('../assets/fav-icon-hover.svg');
	cursor: pointer;
}

.centerPadding {
	padding-top: 1em;
}
