@value colors: './../../../../../common/common.module.css';
@value primaryColor, labelsColor from colors;

.filterContainer {
	width: 9.4em;
	font-size: 16px;
	margin-right: 0.9em;
}

.loadGridFilter {
	padding: 0.3em;
	position: relative;
	border-radius: 6px;
}

.loadGridFilter:hover {
	cursor: pointer;
	background-color: #f3f6f8;
}

.activeToggle,
.activeToggle:hover {
	background-color: primaryColor;
	color: white;
}

.filterText {
	padding-left: 0.4em;
}

.filterDivider {
	display: block;
	height: 1px;
	background-color: rgba(83, 100, 118, 0.2);
	margin: 2px 0;
}
