.deleteConfirmationButton {
	background: transparent !important;
	padding: 0 !important;
}

.deleteConfirmationButton :global(.transition) {
	opacity: 0;
}

.deleteConfirmationButton > :global(.trash.alternate) {
	vertical-align: middle;
	margin: 0 !important;
	width: 30px;
	height: 30px !important;
	background: transparent !important;
	transition: all 0.2s;
	opacity: 1 !important;
}
.deleteConfirmationButton:hover > :global(.trash.alternate) {
	opacity: 0.85 !important;
}

.deleteConfirmationButton > :global(.trash.alternate):before {
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
	width: 24px;
	height: 24px;
	content: url('../../Assets/delete.svg') !important;
	height: 24px;
}

.deleteConfirmationButton:global(.active) {
	filter: brightness(0.8);
}
