.container {
	padding: 20px !important;
}

.labels {
	composes: transition from '../../../common/common.module.css';
	margin: 0 0.5em;
	display: inline-block;
	vertical-align: bottom;
	height: 40px;
	overflow: hidden;
	width: 100%;
}
.labelsHidden {
	composes: labels;
	opacity: 0;
	width: 100%;
	display: none;
}
.label {
	display: flex;
	align-items: center;
	min-height: 30px;
	border-radius: 6px !important;
	background-color: #edf4f9 !important;
	padding: 5px 14px !important;
	font-size: 16px !important;
	font-weight: normal !important;
	line-height: normal !important;
	color: #536476 !important;
}
/* Width consists of left accordion_header padding + icon + side margins = 240px */
.title {
	cursor: pointer;
	font-size: 24px;
	line-height: 1.2;
	font-weight: 600;
	color: #536476;
	margin: 6px 2px 0 12px;
	display: inline-block;
	flex-shrink: 0;
}

.titleFixedWidth {
	width: 166px;
	white-space: nowrap;
	text-overflow: ellipsis;
	overflow: hidden;
}

.header {
	display: flex;
	align-items: flex-start;
}

.content {
	padding-top: 0.875em;
}

.slimContent {
	padding-top: 0;
	padding-left: 5%;
	padding-right: 5%;
	padding-bottom: 3px;
}

.asideActions {
	display: inline-flex;
	align-items: center;
	margin-left: auto;
	margin-top: 6px;
}
.asideActions > div {
	margin-left: 6px !important;
}
.validationIcon {
	composes: transition from '../../../common/common.module.css';
	width: 24px;
	height: 24px;
	margin-left: 6px;
}
.validationIconHidden {
	composes: validationIcon;
	opacity: 0;
}

@media only screen and (max-width: 850px) {
	.labels {
		display: none;
	}
}

.arrowButton {
	height: 30px !important;
	width: 30px !important;
	margin-top: 4px !important;
	border-radius: 4px !important;
	display: flex;
	align-items: center;
	justify-content: center;
	margin-right: 0 !important;
	box-shadow: none !important;
	flex-shrink: 0;
}

.arrowButtonDefault.arrowButton {
	height: 30px !important;
	background-color: #7e92a3 !important;
}

.arrowButtonDefault.arrowButton:hover {
	background-color: #6d7c8b !important;
}

.arrowIcon {
	transition: transform 0.2s ease-in;
}

.arrowIconActive.arrowIcon {
	transform: rotate(90deg);
}

.alertIcon {
	composes: alertIcon from '../elements/CoreIcon/CoreIcon.module.css';
}

.noPadding {
	composes: noPadding from '../input/CoreButton/CoreButton.module.css';
}

.accordionDeleteButton {
	composes: noPadding;
	width: 24px !important;
	height: 24px !important;
}
.accordionDeleteButton :global .trash.alternate.icon {
	width: 24px !important;
	height: 24px !important;
}
.accordionDeleteButton :global .trash.alternate.icon {
	width: 24px !important;
	height: 24px !important;
}
