.periodOval {
	width: auto;
	height: 27px;
	border-radius: 13px;
	background-color: #2ba6df;
	cursor: pointer;
	padding-left: 7px;
	margin-top: -10px;
	z-index: 2;
}

.futureOval {
	background-color: #a8b1ba;
}

.alertOval {
	background-color: #f82056;
}

.periodChain {
	display: inline-block;
	width: -webkit-fill-available;
	margin-top: 15px;
}

.ovalText {
	font-family: 'Source Sans Pro', sans-serif !important;
	font-size: 16px;
	font-weight: 400;
	font-stretch: normal;
	font-style: normal;
	line-height: 1.25;
	letter-spacing: normal;
	color: #ffffff;
	bottom: 10px;
	white-space: nowrap;
	vertical-align: -webkit-baseline-middle;
	margin-left: 5px;
}

.arrowIcon {
	transform: rotate(-90deg);
	transition: transform 0.2s ease-in;
}

.arrowIconActive.arrowIcon {
	transform: rotate(0deg);
}

.inline {
	display: inline-block;
}

.block {
	display: block;
	float: right;
	padding-top: 3px;
	padding-right: 3px;
}

.periodEventsContainer {
	width: -webkit-fill-available;
	padding-left: 20px;
	margin-left: 25px;
	border-left: 5px solid #2ba6df;
}

.futureEventsContainer {
	border-left: 5px solid #a8b1ba;
}
.alertEventsContainer {
	border-left: 5px solid #f82056;
}

.behind {
	z-index: 1;
}

.periodEventsContainer ul {
	list-style: none; /* Remove default bullets */
	margin-block-start: 0em;
	margin-block-end: 0em;
	padding-left: 0px;
	border-left: 2px solid #a8b1ba;
	list-style-position: outside;
	padding-bottom: 1em;
}

.periodEventsContainer ul li::before {
	content: '\2022'; /* Add content: \2022 is the CSS Code/unicode for a bullet */
	font-weight: bold;
	font-size: 2em;
	display: inline-block;
	width: 1em;
	margin-left: -6px;
}
.periodEventsContainer ul li {
	height: 3em;
	display: table-row;
}

.periodEventsContainer ul li span {
	margin-top: 0px;
	margin-bottom: 0px;
	display: table-cell;
}

.periodEventsContainer ul li:last-child {
	padding-bottom: 10px;
}

.periodEventsContainer ul li span:last-child {
	margin-bottom: 3px;
}

.futureText::before {
	color: #a8b1ba !important;
}

.warningText::before {
	color: #e0ac47 !important;
}

.alertText::before {
	color: #f82056 !important;
}

.normalText::before {
	color: #2ba6df !important;
}

.periodEventCountText {
	width: 22px;
	height: 22px;
	padding: 1px 2.5px 1px 3.5px;
	background-color: #56c2f4;
	font-family: 'Source Sans Pro', sans-serif !important;
	font-size: 12px;
	font-weight: 400;
	font-stretch: normal;
	font-style: normal;
	line-height: 1.67;
	letter-spacing: normal;
	text-align: center;
	color: #ffffff;
	border-radius: 13px;
	display: inline-block;
	float: right;
	margin-right: 2px;
	margin-top: 2px;
}

.periodEventCountFutureText {
	background-color: #d7e0e7;
}
.periodEventCountAlertText {
	background-color: #fa5680;
}
.timelineHeader {
	width: 100%;
	height: 30px;
	margin-bottom: 10px;
}

.timelineHeader > :global(.ui.blue.right.corner.label) {
	border-color: #2ba6df !important;
}
