/* Customize the label (the container) */
.container {
	display: block;
	position: relative;
	padding-left: 1.8em;
	cursor: pointer;
	font-size: 16px;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
	width: 9em;
}

/* -- Reduce tr checkbox width -- */
tbody tr td .container {
	width: 0 !important;
}

/* Hide the browser's default radio button */
.container :global(input) {
	position: absolute;
	opacity: 0;
	cursor: pointer;
	height: 0;
	width: 0;
}

/* Create a custom radio button */
.checkmark {
	position: absolute;
	top: 0;
	left: 0;
	height: 20px;
	width: 20px;
	background-color: #e3e3e3;
	border-radius: 50%;
}

/* On mouse-over, add a grey background color */
.container input ~ .checkmark,
.container:hover input ~ .checkmark,
.container input:focus ~ .checkmark {
	background-color: #e3e3e3;
}

/* When the radio button is checked, add a green background */
.container input:checked ~ .checkmark {
	background-color: #80c17d;
	border: solid 1px #cec9c9;
}

/* When the radio button is disabled, add an opacity */
.container input:checked:disabled ~ .checkmark {
	background-color: #80c17d;
	opacity: 0.5;
}

.container input:disabled ~ .checkmark {
	opacity: 0.5;
}

/* When the radio button is disabled, add an opacity to appropriate Label */
.controlsDisabled {
	opacity: 0.5;
}

.radioLabel {
	font-family: 'Source Sans Pro', sans-serif !important;
	letter-spacing: normal;
	color: #536476;
	font-weight: 500;
	line-height: normal;
}
