.searchToggles {
	margin-left: auto;
	display: flex;
	justify-content: flex-end;
}

.container {
	display: flex;
	flex-wrap: wrap-reverse;
}

.quickTasksContainer {
	margin-bottom: 20px;
	width: 100%;
}

@media (min-width: 1350px) {
	.quickTasksContainer {
		margin-bottom: 0;
		margin-left: 20px;
		width: auto;
	}

	.headerContainer {
		width: auto;
	}

	.container {
		flex-wrap: initial;
	}
}

.headerContainer {
	width: 80%;
}
