.mciDisplayHorizontalContainer {
	display: flex;
	align-items: center;
}

.infoIcon {
	align-self: flex-start;
}
.mciDonutContainer {
	display: inline-block;
}
.block {
	display: inline-block;
}

.mciLabelText {
	display: inline-block;
	padding-right: 5px;
}

.mciLabelTextVertical {
	text-align: center;
	margin-bottom: 5px;
}

.mciLabelContainer {
	display: inline-block;
	width: 130px;
}
.mciToolTip {
	display: inline-block;
	padding-top: 1em;
	width: 60%;
}
.mciLabelWideContainer {
	display: inline-block;
	width: 150px;
}
.mciLabelWideContainer p {
	margin-bottom: 0.1em !important;
	width: 100%;
}
.mciLabelContainer p {
	margin-bottom: 0.1em !important;
	width: 100%;
}
.labelText {
	justify-content: left;
}
.currencyText {
	font-weight: 600;
	justify-content: right !important;
	float: right;
}

.mciLabelVerticalContainer {
	padding-left: 12%;
	padding-right: 12%;
}

/*
 * source: https://css-tricks.com/centering-in-the-unknown/
 */
.donutVerticalContainer {
	display: table;
	width: 100%;
}

.donutVerticalContainer > * {
	display: table-cell;
	text-align: center;
	vertical-align: middle;
}

.donutVerticalContainer > *:first-child {
	padding-left: 27px;
}
