.compactItem {
	line-height: 42px;
	background-color: #d7e0e7;
	border-radius: 6px;
	width: calc(100% - 40px);
	display: inline-block;
	margin-left: 40px;
	margin-bottom: 6px;
}

.compactItemLeftColumn {
	vertical-align: middle;
	color: #a8b1ba !important;
}

.compactItemRightColumn {
	vertical-align: middle;
	color: #a8b1ba !important;
}

.compactItemCenterColumn {
	padding-left: 10px;
	vertical-align: middle;
	font-size: 22px;
	color: #7e92a3 !important;
}

.deleteConfirmationButton {
	float: right;
	vertical-align: middle;
	padding-top: 8px;
	padding-right: 10px;
	content: url('../../core/components/Assets/delete.svg') !important;
}

.orange > :global(i.yellow.icon) {
	color: #ce9520 !important;
}

.draggingItem {
	opacity: 0;
}

.sortingItem {
	display: flex;
	align-items: center;
	height: 47px;
	padding-right: 40px;
	padding-left: 63px;
	margin: 10px 0;
	font-size: 18px;
	border-radius: 6px;
	color: #6d7c8b;
	background-color: #dbe8f1;
	cursor: move;
}

.deleteItem {
	position: absolute;
	z-index: 2;
	right: 15px;
	width: 25px !important;
	height: 23px !important;
	padding: 0 !important;
	background-color: transparent !important;
}
