.removeMargin > :global(.field) {
	margin-bottom: 0 !important;
}

.citySearch:global(.focus > div div.ui.dropdown),
.citySearch:global(.focus > div input) {
	border-color: #00d2fa !important;
}

/** Combined component **/
.inputsContent {
	display: inline-flex;
	justify-content: center;
	width: 100% !important;
}

/** input **/
.inputsContent > div > div {
	width: 100% !important;
}

.inputsContent > div > div > input {
	padding-right: 0 !important;
	border-right: none !important;
	border-top-right-radius: 0 !important;
	border-bottom-right-radius: 0 !important;
}

.inputArea {
	width: 100% !important;
}

/** dropdown **/
.inputsContent > div + div {
	max-height: 50px !important;
	margin-right: 0 !important;
	border-top-left-radius: 0px !important;
	border-bottom-left-radius: 0px !important;
	border-bottom: 2px solid !important;
	border-top: 2px solid !important;
	border-right: 2px solid !important;
	background-color: rgb(255, 255, 255) !important;
	border-color: #d7e0e7 !important;
}

/** Combined component **/
.inputsContentError {
	display: inline-flex;
	justify-content: center;
	width: 100% !important;
}

/** input **/
.inputsContentError > div > div {
	width: 100% !important;
}

.inputsContentError > div > div > input {
	padding-right: 0 !important;
	border-right: none !important;
	border-top-right-radius: 0 !important;
	border-bottom-right-radius: 0 !important;
}

/** dropdown **/
.inputsContentError > div + div {
	max-height: 50px !important;
	margin-right: 0 !important;
	border-top-left-radius: 0px !important;
	border-bottom-left-radius: 0px !important;
	border-bottom: 2px solid !important;
	border-top: 2px solid !important;
	border-right: 2px solid !important;
	border-color: #f49c9e !important;
	background-color: #ffeff0 !important;
}
.dropdownIcon {
	padding: 0 !important;
}
.dropdownIcon:global(.ui.dropdown > i.dropdown.icon:not(.clear):before) {
	content: url(../../core/components/Assets/down-chevron.svg) !important;
}

.dropdownIcon:global(.ui.dropdown > .dropdown.icon.clear:before) {
	content: url(../../core/components/Assets/close.svg) !important;
}

.dropdownIcon:global(.ui.loading.selection.dropdown > i.icon) {
	height: 100% !important;
}

.dropdownIcon:global(.ui.dropdown > .dropdown.icon) {
	padding: 0 !important;
	height: 100% !important;
	display: flex;
	align-items: center;
	padding: 0 4px !important;
}

.dropdownIcon:global(.ui.dropdown.active > .dropdown.icon:not(.clear)) {
	transition: transform ease-in 0.2s;
	transform: rotate(180deg);
}
