.menuItem {
	composes: transition from '../../../../common/common.module.css';
	transition-timing-function: ease;
	text-decoration: none;
	position: relative;
	font-size: 16px;
	color: #fff;
	display: flex;
	align-items: center;
	height: 26px;
	width: 100%;
	font-weight: 500;
	border-bottom: 1px solid rgb(189, 198, 205, 0.1);
	cursor: pointer;
}

@media screen and (min-height: 600px) {
	.menuItem {
		height: 34px;
	}
	.dropdownIcon :global(.icon) {
		margin-top: 7px;
	}
}

@media screen and (min-height: 768px) {
	.menuItem {
		height: 50px;
	}
	.dropdownIcon :global(.icon) {
		margin-top: 15px;
	}
}

.menuItem:hover {
	background: rgba(0, 0, 0, 0.3);
	color: #fff;
}

.menuItem:active {
	outline: 0;
}

.menuItem:after {
	content: '';
	display: block;
	position: absolute;
	left: 0;
	top: 0;
	width: 0;
	height: 100%;
	background: #21ba45;
}

.menuItem:hover:after {
	width: 4px;
}

.active {
	background: rgba(0, 0, 0, 0.3);
	color: #fff;
	opacity: 1 !important;
	outline: 0;
}

.active:after {
	width: 4px;
}

.icon {
	opacity: 0.5;
	font-size: 18px;
	margin-left: 20px;
	margin-right: 10px;
}

.active .icon {
	opacity: 1;
}

.label {
	composes: transition from '../../../../common/common.module.css';
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
	width: 150px;
	color: white;
}

.labelHidden {
	composes: label;
	opacity: 0;
}

.subMenuItem {
	composes: transition from '../../../../common/common.module.css';
	transition-timing-function: ease;
	text-decoration: none;
	position: relative;
	font-size: 14px;
	color: #fff;
	display: flex;
	align-items: center;
	height: 28px;
	width: 100%;
	font-weight: 400;
	padding-left: 20px;
	background: rgba(0, 0, 0, 0.3);
	border-bottom: 1px solid rgb(189, 198, 205, 0.1);
	cursor: pointer;
}

.subMenuItem:hover {
	background: rgba(0, 0, 0, 0.3);
	color: #fff;
}

.subMenuItem:active {
	outline: 0;
}

.subMenuItem:after {
	content: '';
	display: block;
	position: absolute;
	left: 0;
	top: 0;
	width: 0;
	height: 100%;
	background: #41cc79;
}

.subMenuItem.active:after {
	width: 4px !important;
}

.subMenuItem:hover:after {
	width: 4px;
}

.subMenuItem:before {
	content: '+';
	display: block;
	position: absolute;
	right: 20px;
	top: 5px;
	height: 100%;
	font-weight: bolder;
}
.subMenuItem.active:before {
	content: '-';
	display: block;
	position: absolute;
	right: 20px;
	top: 5px;
	height: 100%;
	font-weight: bolder;
}

.subMenuItem > div {
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
	padding-right: 27px;
}

.categoryItem a {
	color: white !important;
	width: 100%;
}

.categoryItem {
	composes: transition from '../../../../common/common.module.css';
	transition-timing-function: ease;
	text-decoration: none;
	position: relative;
	font-size: 13px;
	color: #fff;
	display: flex;
	align-items: center;
	height: 28px;
	width: 100%;
	max-width: 210px;
	font-weight: 400;
	padding-left: 30px;
	white-space: nowrap !important;
	background: rgba(0, 0, 0, 0.3);
	border-bottom: 1px solid rgb(189, 198, 205, 0.1);
	cursor: pointer;
}

.categoryItem:hover {
	background: rgba(0, 0, 0, 0.3);
	color: #fff;
}

.categoryItem:active {
	outline: 0;
}

.categoryItem:after {
	content: '';
	display: block;
	position: absolute;
	left: 0;
	top: 0;
	width: 0;
	height: 100%;
	background: #41cc79;
}

.categoryItem.active:after {
	width: 4px !important;
}

.categoryItem:hover:after {
	width: 4px;
}

.dropdownIcon {
	composes: menuItem;
	font-size: 18px;
	width: 100%;
}

.dropdownIcon:hover {
	background: rgba(0, 0, 0, 0.3);
	color: #fff;
}

.dropdownIcon :global(.icon) {
	font-size: 18px;
	opacity: 0.5;
	margin-left: 20px;
}

.dropdownIcon :global(.menu.transition.visible) {
	background-color: #1b2e3d;
	position: absolute;
	margin-left: 60px;
	margin-top: -50px;
	margin-bottom: -50px;
}

.dropdownIcon :global(.menu.transition.visible) > :global(.item.active) {
	font-weight: inherit;
}

.dropdownIcon
	:global(.menu.transition.visible)
	:global(.menu.transition.visible) {
	margin-left: 10px !important;
}

.dropdownIcon :global(.menu.transition.visible) > :global(.item) {
	color: white !important;
	margin: 5px 10px;
	padding: 0 !important;
}

.dropdownIcon :global(.menu.transition.visible) > :global(.item.dropdown) {
	padding: 8px !important;
}

.dropdownIcon :global(.menu.transition.visible) > :global(.item) > a {
	display: inline-block;
	vertical-align: top;
	width: 100%;
	padding: 8px;
	color: white !important;
}

.dropdownIcon :global(.menu.transition.visible) > :global(.item):hover {
	background-color: #264259;
	border-radius: 5px;
}

.scrollMenu {
	max-height: 170px;
	overflow-y: auto;
	overflow-x: hidden;
}

.scrollDropdown {
	max-height: 255px;
	overflow-y: auto;
	overflow-x: hidden;
	bottom: auto !important;
}

.ellipsisOverflow {
	text-overflow: ellipsis;
}

.scrollMenu::-webkit-scrollbar,
.scrollDropdown::-webkit-scrollbar {
	width: 8px;
}
.scrollMenu::-webkit-scrollbar-thumb,
.scrollDropdown::-webkit-scrollbar-thumb {
	border-radius: 2px;
	background: #d7e0e7 !important;
}
