.dottedIcon {
	content: '';
	position: absolute;
	z-index: 2;
	left: 7px;
	width: 36px;
	height: 36px;
	pointer-events: none;
}

.dottedIcon :global i {
	content: '';
	position: absolute;
	z-index: 2;
	width: 5px;
	height: 5px;
	border-radius: 50%;
	background-color: #6d7c8b;
	opacity: 0.3;
}
.dottedIcon :global i:first-child {
	top: 8px;
	left: 11px;
}
.dottedIcon :global i:nth-child(2) {
	top: 8px;
	right: 11px;
}
.dottedIcon :global i:nth-child(3) {
	left: 11px;
	top: 16px;
}
.dottedIcon :global i:nth-child(4) {
	right: 11px;
	top: 16px;
}
.dottedIcon :global i:nth-child(5) {
	left: 11px;
	bottom: 7px;
}
.dottedIcon :global i:last-child {
	right: 11px;
	bottom: 7px;
}
