.input {
	padding: 16px 14px 0px 14px !important;
	font-size: 16px !important;
	line-height: 1.19 !important;
	border: 2px solid #dde4eb !important;
	border-radius: 6px !important;
	color: #202a45 !important;
}

.input:not(textarea) {
	height: 50px !important;
}

textarea.input {
	min-height: 50px !important;
}

.inputError {
	composes: input;
	border-color: #f49c9e !important;
	background-color: #ffeff0 !important;
}

.inputLabel {
	composes: transition from './common.module.css';
	white-space: nowrap;
	position: absolute;
	font-weight: normal !important;
	left: 16px;
	right: 17px;
	top: 16px;
	z-index: 1;
	pointer-events: none;
	overflow: hidden;
	text-overflow: ellipsis;
	font-size: 16px !important;
	line-height: 1.19 !important;
	color: #6d7c8b !important;
	margin: 0 !important;
}

.inputLabel:after {
	margin-left: 0 !important;
}

.inputLabelActive {
	composes: inputLabel;
	transform: translateY(-9px) !important;
	font-size: 12px !important;
	font-weight: 600 !important;
	color: #909090 !important;
	line-height: normal !important;
	letter-spacing: 0.25px;
	opacity: 0.6;
}

.inputFocused {
	box-shadow: 0 0 2px 3px rgba(186, 217, 253, 0.5);
	border: solid 2px #00d2fa !important;
}

.inputDisabled {
	background-color: #f9f9fb !important;
	border: solid 2px #dee2e9 !important;
	opacity: 1 !important;
	color: rgba(32, 42, 69, 0.45) !important;
}

.inputHover {
	border: solid 2px #a8b1ba !important;
}

.inputFilled {
	border-color: #d7e0e7 !important;
}
