.quickTasks {
	border-radius: 6px;
	box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.1);
	background-color: #80c17d;
	padding: 20px 20px;
	/*min-width: 446px;*/
	max-width: 446px;
}

.quickTasksMinimized {
	padding-top: 10px;
	padding-bottom: 10px;
	/*min-width: 446px;*/
}

.title {
	font-size: 16px !important;
	font-weight: bold !important;
	color: #ffffff !important;
	line-height: normal !important;
	margin: 0 0 8px !important;
	text-transform: uppercase;
	display: none;
}

.search {
	display: flex;
	flex: 1;
	position: relative;
	/*min-width: 406px;*/
}

.search :global(.field) {
	width: 100%;
}

.search :global(.ui.icon.input > input) {
	padding-right: 34px !important;
}

.searchMinimized {
	/* TODO move to const */
	height: 30px;
	position: relative;
	padding-right: 32px;
	border-radius: 6px;
	border: solid 2px #dde4eb;
	overflow: hidden;
}

.searchMinimized :global(.field input) {
	padding-top: 0 !important;
	height: 26px !important;
	font-size: 16px !important;
	line-height: 1.19 !important;
	border: none !important;
	border-radius: 0 !important;
	border-right: solid 2px #dde4eb !important;
}

.chevronIconContainer {
	position: absolute;
	top: 0;
	bottom: 0;
	right: 0;
	width: 32px;
	background-color: #80c17d;
	display: flex;
	justify-content: center;
}

.chevronIconContainer:hover {
	background-color: #72ad70;
}

.searchMinimized :global(.field input::placeholder) {
	opacity: 0.61;
	color: #6d7c8b;
}

.searchMinimized :global(.dropdown) {
	padding: 0 !important;
}

.actions {
	display: flex;
	/*flex-direction: row;*/
	justify-content: space-between;
	min-width: 100%;
}

.innerContainer {
	display: flex;
	align-items: center;
	flex-wrap: wrap;
	flex-direction: row;
}
.innerContainer > * {
	margin: 13px 0;
}

.innerContainerMinimized {
	display: flex;
}

.actionBtn {
	border: solid 2px #a6d3a4 !important;
	margin: 0px 5px !important;
	padding: 10px !important;
	font-size: 1rem !important;
	height: auto !important;
	white-space: nowrap;
	width: 100%;
}

.actionBtn:last-child {
	margin-right: 0px !important;
}

.actionBtn:first-child {
	margin-left: 0px !important;
}

.actionBtn:hover {
	background-color: #72ad70 !important;
}

.linkButton {
	width: 100%;
	margin-left: 20px !important;
}

.searchIconBtn {
	height: 26px;
	background-color: transparent !important;
	position: absolute;
	right: 6px;
}

.searchIconBtnMinimized {
	padding: 0 !important;
	margin: 0 !important;
}

.enhancedButtonMinimized {
	margin-left: 10px;
}

.enhancedButtonMinimized:hover {
	cursor: pointer;
}

@media (min-width: 1350px) {
	.quickTasks {
		min-width: 244px;
	}

	.actions {
		margin-top: 14px;
	}

	.linkButton {
		margin-left: 0 !important;
	}
}

