.headerPrimary > tr > th {
	background-color: #264259 !important;
	white-space: nowrap;
	color: white !important;
	position: sticky !important;
	top: 0;
	z-index: 5;
	font-weight: 600 !important;
	height: 50px;
	font-family: 'Source Sans Pro', sans-serif !important;
	font-size: 16px;
}

.headerDefault {
	background-color: gray;
	white-space: nowrap;
	color: white;
	font-weight: 400;
}

.pointer {
	cursor: pointer;
}

.centered {
	position: relative;
	top: 50%;
	transform: translateY(-50%);
}

.pagingIcon {
	cursor: pointer;
	padding-left: 10px;
	padding-right: 10px;
}

.pagingIconDisabled {
	color: lightgrey;
	cursor: default;
	padding-left: 10px;
	padding-right: 10px;
}

.unselectable {
	-webkit-touch-callout: none;
	-webkit-user-select: none;
	-khtml-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
}

.pagingFont {
	font-family: 'Source Sans Pro', sans-serif !important;
	font-weight: 700;
	font-size: 14px;
}

.scroll {
	overflow-y: auto;
	width: 100%;
}
/* table on /tms/loads start */
:global(.minimizedShipmentBuckets .bucket-grid) .scroll {
	max-height: calc(100vh - 340px);
}

@media (max-width: 1400px) {
	:global(.minimizedShipmentBuckets .bucket-grid) .scroll {
		max-height: calc(100vh - 420px);
	}
}
@media (max-width: 1025px) {
	:global(.minimizedShipmentBuckets .bucket-grid) .scroll {
		max-height: unset;
	}
}
:global(.notMinimizedShipmentBuckets .bucket-grid) .scroll {
	max-height: calc(100vh - 410px);
}

@media (max-width: 1400px) {
	:global(.notMinimizedShipmentBuckets .bucket-grid) .scroll {
		max-height: unset;
	}
}
/* table on /tms/loads end */

.scrollSmall {
	max-height: 495px;
	overflow-y: auto;
	width: 100%;
}

.right {
	text-align: right !important;
}
.left {
	text-align: left !important;
}

.ellipsis {
	max-width: 100px;
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
}

.noLRBorder > tr > td {
	border: 0px !important;
}

.noPseudo:after {
	display: none !important;
}

.rowSortable:before {
	display: inline-block;
	position: relative;
	top: 2px;
	right: 6px;
	font-family: Icons;
}

.colSortableDefault:before {
	content: url('./assets/grid-column-default.svg');
}

.colSortedAscend:before {
	content: url('./assets/grid-column-ascend.svg');
}

.colSortedDesc:before {
	content: url('./assets/grid-column-descend.svg');
}

.tableBody {
	font-size: 16px;
	font-family: 'Source Sans Pro', sans-serif !important;
	line-height: 1.13;
	color: #467599;
	background-color: white;
}

/* --- Need to set it explicitly, because of unscrollable first columns --- */
.tableBody tr {
	background-color: #ffffff;
}

.tableBody tr:hover {
	background-color: #f3f3f3;
}

/* --- Helps to make scrollable tr --- */
.tableWhole {
	border-left: none !important;
	border-right: none !important;
}

/* --- Scroll when we dont have label in first th in table header --- */
.nonScrollableDoubleItem tr th:first-child,
.nonScrollableSingleItem tr th:first-child {
	position: sticky;
	left: 0;
	right: 0;
	z-index: 6;
	max-width: 100px;
	padding: 0;
}

.nonScrollableDoubleItem tr th:nth-child(2) {
	left: 60px;
	z-index: 6;
}

/* --- Scroll when only one label in table header --- */
.nonScrollableSingleColumns tr td:first-child:not([colspan]),
.nonScrollableDoubleColumns tr td:first-child:not([colspan]) {
	position: sticky;
	left: 0;
	right: 0;
	background: inherit;
	z-index: 3;
	max-width: 100px;
	padding: 0;
	transition: background-color 0s !important;
}

.nonScrollableDoubleColumns tr td:nth-child(2) {
	position: sticky;
	right: 0;
	background: inherit;
	left: 60px;
	z-index: 3;
	padding: 0;
	transition: background-color 0s !important;
}

/* --- Fade scroll line --- */
@media (max-width: 1280px) {
	.nonScrollableSingleColumns tr td:first-child:not([colspan]):after,
	.nonScrollableDoubleColumns tr td:nth-child(2):after {
		top: 0;
		content: '|';
		width: 24px;
		position: absolute;
		left: 60px;
		color: transparent;
		bottom: 0;
		opacity: 0.15;
		background-image: linear-gradient(to left, rgba(0, 0, 0, 0) 65%, #000000);
	}
}

/* --- Remove border-radius on the top corners of the header --- */
.borderStrict:first-child,
.borderStrict:last-child {
	border-radius: 0 0 0 0 !important;
}

