.fieldset {
	border: 0;
	margin: 0;
	padding: 0;
}

.legend {
	display: flex;
	align-items: center;
	text-transform: uppercase;
	font-size: 1.1rem;
	font-size: 16px;
	color: #4a4a4a;
	position: relative;
	top: 50%;
	transform: translateY(-50%);
}

.hr {
	border-color: #d5d5d5;
	border: 0;
	border-top: 1px solid #ccc;
}

.floatRight {
	float: right;
}
