.message {
	font-weight: 600;
	font-size: 12px;
	color: #467599;
	text-align: left;
	white-space: pre-line;
}
.lightMessage {
	font-weight: 600;
	font-size: 12px;
	color: #a8b1b8;
	text-align: left;
	white-space: pre-line;
}
.darkMessage {
	font-weight: 600;
	font-size: 12px;
	color: #467599;
	text-align: left;
	white-space: pre-line;
}
.lightAlertIcon {
	vertical-align: top;
	width: 1.4em !important;
	height: 1.2em !important;
	background-image: url('../CoreIcon/Assets/info-icon-light.svg') !important;
	background-position: center;
	background-size: cover;
}
.smallAlertIcon:before {
	content: none !important;
}

.darkAlertIcon {
	vertical-align: sub;
	width: 1.4em !important;
	height: 1.2em !important;
	background-image: url('../CoreIcon/Assets/info-icon-dark.svg') !important;
	background-position: center;
	background-size: cover;
}
.smallAlertIcon:before {
	content: none !important;
}
