.container {
	border-radius: 6px !important;
	border: solid 2px #dde4eb !important;
	height: 30px !important;
	min-height: 30px !important;
	padding-top: 5px !important;
	padding-bottom: 4px !important;
}

.container :global(.text) {
	font-size: 16px !important;
	color: #6d7c8b !important;
}

.container :global(.dropdown.icon) {
	top: 9px !important;
	right: 14px !important;
	padding: 0 !important;
}

.container :global(.dropdown.icon:before) {
	content: url('./assets/down-chevron.svg') !important;
}

:global(.ui.selection.active.dropdown .menu) {
	border-bottom-left-radius: 6px !important;
	border-bottom-right-radius: 6px !important;
	box-shadow: 0 4px 13px 0 #cccccc !important;
	border: solid 1px #dde4eb !important;
}
