.loadCheckCallCarrierInfo {
	float: right;
	margin-bottom: 5px;
	font-size: 1em;
	color: #536476;
	text-align: right;
}

.unlockButton {
	float: right;
	width: 24px;
	height: 24px;
	margin-top: 10px;
}

.checkCallTile {
	min-height: 28em !important;
}

.disableResize {
	resize: none !important;
}

.dispatchHeaders {
	font-size: 0.9em;
	font-family: Lato, 'Helvetica Neue', Arial, Helvetica, sans-serif;
	line-height: 1.28571429em;
	margin: calc(2rem - 0.14285714em) 0 1rem;
	font-weight: 700;
	padding: 0;
}

.carrierName {
	color: #536476;
}

.floatRight {
	float: right;
}
.addIcon {
	top: 30%;
	width: 22px;
	height: 22px;
	background-color: #80c17d;
	border-radius: 50%;
	display: flex;
	justify-content: center;
	align-items: center;
	color: #fff;
	font-size: 19px;
	cursor: pointer;
}
.addIconDisable {
	composes: addIcon;
	opacity: 0.7;
	pointer-events: none;
	cursor: pointer !important;
}
.addIcon:hover {
	background-color: #72ad70;
}
.contactGrid {
	border-top: 1px solid rgba(34, 36, 38, 0.15);
	width: 100%;
	margin-bottom: 2em;
	margin-top: 2em;
}

.buttonRow {
	margin-top: 2em;
}

.contactGridText {
	font-family: 'Source Sans Pro', sans-serif !important;
	font-size: 16px;
	font-weight: 600;
	font-stretch: normal;
	font-style: normal;
	line-height: normal;
	letter-spacing: normal;
	color: #536476;
	margin-block-end: 0px;
	margin-block-start: 0px;
}

.contactGridText p {
	margin-block-end: 0px;
	margin-block-start: 0px;
}

.driverTitle {
	color: #80c17d;
	cursor: pointer;
	padding-right: 1rem;
}

.dropdownIcon {
	left: 1rem;
	z-index: 1;
}

.noSpacing {
	margin-block-start: 0px;
	margin-block-end: 0px;
	width: 150px;
}
.driverNumber {
	clear: both;
}

.dropdownOption {
	display: flex;
	margin-left: 20px;
	margin-right: 20px;
	align-items: center;
	font-family: 'Source Sans Pro', sans-serif !important;
	font-size: 16px;
	font-weight: 400;
	font-stretch: normal;
	font-style: normal;
	line-height: normal;
	letter-spacing: normal;
	color: #536476;
}

.removeIcon {
	top: 32%;
	line-height: 1;
	vertical-align: middle;
	font-size: 1.5em;
	color: #6d7c8b;
}

.noWordWrap {
	white-space: initial;
}
