.menuContainer {
	composes: transition from '../../../common/common.module.css';
	height: 100%;
	position: fixed;
	z-index: 12;
	top: 0;
	left: 0;
	background-color: #264259;
	width: 60px;
}

.menuContainerExpanded {
	composes: menuContainer;
	width: 210px;
}

.ui.upward.dropdown > .menu {
	top: auto;
	box-shadow: 0 0 3px 0rgba (0, 0, 0, 0.08);
	border-radius: 0.28571429rem 0.28571429rem 0 0;
}

.menuContainerExpanded .menuItemsWrapper {
	max-height: calc(100% - 60px);
	overflow-y: auto;
}

.menuItemsWrapper::-webkit-scrollbar {
	width: 8px;
}
.menuItemsWrapper::-webkit-scrollbar-thumb {
	border-radius: 2px;
	background: #d7e0e7 !important;
}
