.headerActionsButton {
	margin: 1.3rem 1.3rem 1rem 0;
	display: inline-block;
	vertical-align: middle;
}
.headerActionsIconIntention {
}

.headerActionsButton :global(.button.dropdown) {
	border-radius: 25px !important;
}

.headerActions :global(.button.dropdown) :global(.icon) {
	left: 4px;
}

.popup {
	border-radius: 6px;
	box-shadow: 0 4px 13px 0 #cccccc;
	border: solid 1px #dde4eb;
	background-color: #ffffff;
	overflow: hidden;
	margin-top: 0 !important;
	left: -8px !important;
	width: 230px;
}

.headerActionsItemHeader {
	text-transform: uppercase;
	pointer-events: none;
	font-size: 14px;
	font-weight: 600;
	font-stretch: normal;
	font-style: normal;
	line-height: 1.46;
	letter-spacing: normal;
	color: #536476;
	margin-left: 20px;
}

.popup :global(.content) {
	padding: 0 !important;
}

.popup :global(.header) {
	display: none;
}

.headerActionsIconButton {
	width: 24px;
	height: 24px;
	padding: 0 !important;
	background-color: #fff !important;
}

.headerActionsIconButton :global(.info.icon),
.headerActionsIconButton :global(.info.icon:before) {
	font-size: 12px;
	font-family: inherit;
	text-align: center;
	font-weight: bold;
	margin: 0 !important;
	content: 'i';
	left: 0 !important;
	line-height: 13px;
	opacity: 1;
}

.headerActionsIconButton :global(.text),
.headerActionsIconButton :global(.visible.menu) {
	display: none !important;
}

.dropdownItem {
	padding: 5px 15px !important;
	display: flex;
	align-items: center;
}

.dropdownItem:hover {
	background-color: #ecf2f6 !important;
}

.dropdownItem :global(.text) {
	margin-left: 40px;
	padding: 0 !important;
	font-size: 14px;
	font-weight: normal;
	font-stretch: normal;
	font-style: normal;
	line-height: 1.36;
	letter-spacing: normal;
	color: #536476;
}
