.greentext {
	color: #80c17d;
	font-weight: bold;
}

.graytext {
	color: #7f8fa6;
	font-weight: bold;
}

.widgetsegment {
	padding: 1rem;
}

.thisweek:after {
	content: '';
	width: 0;
	height: 100%;
	position: absolute;
	border: 1px solid #eee;
	top: 0;
	left: 110%;
}

.widgetnumbers {
	font-size: 2.8rem;
	margin: 0;
	padding-bottom: 2px;
	font-weight: bold;
}

.dollarsign {
	font-size: 1.6rem !important;
	font-weight: normal;
	color: #7f8fa6;
	vertical-align: text-bottom;
	padding: 0.2rem;
}
