.ribbon {
	width: 200px;
	position: fixed;
	top: 25px;
	right: -50px;
	left: auto;
	text-align: center;
	line-height: 45px;
	color: rgb(0, 0, 0);
	font-size: 15px;
	text-transform: uppercase;
	transform: rotate(45deg);
	-webkit-transform: rotate(45deg);
	box-shadow: 0 0 3px rgba(0, 0, 0, 0.3);
	z-index: 11;
}

.blueRibbon {
	composes: ribbon;
	background-color: #071d49;
	color: #ffffff;
}

.greenRibbon {
	composes: ribbon;
	background-color: #6cc24a;
	color: #ffffff;
}

.tealRibbon {
	composes: ribbon;
	background-color: #09968f;
	color: #ffffff;
}
