svg.pulse {
	overflow: visible;
}

.firstCircle {
	fill: #f00;
	transform: scale(0.5);
	transform-origin: center center;
	animation: pulse-me 3s linear infinite;
}
.secondCircle {
	composes: firstCircle;
	animation-delay: 1s;
}
.thirdCircle {
	composes: firstCircle;
	animation-delay: 2s;
}

@keyframes pulse-me {
	0% {
		transform: scale(0.5);
		opacity: 0;
	}
	50% {
		opacity: 0.1;
	}
	70% {
		opacity: 0.09;
	}
	100% {
		transform: scale(5);
		opacity: 0;
	}
}
