@value colors: '../common.module.css';
@value primaryColor, labelsColor from colors;

.secondaryInformation {
	margin: 0 2rem 0 0;
	line-height: 20px;
	font-size: 16px;
	color: #6d7c8b;
}

.secondaryInformation:not(:first-child):before {
	content: '';
	position: absolute;
	left: -16px;
	top: 10px;
	display: block;
	width: 4px;
	height: 4px;
	border-radius: 50%;
	background-color: #6d7c8b;
}

.headerLink {
	color: primaryColor !important;
	font-size: 16px;
	margin-left: 0.25em;
}
