.itemCounter {
	color: #4a4a4a;
	font-size: 14px;
	padding: 0 1.5em;
	display: inline;
}

.itemCounterColor {
	color: #41cc79;
	font-weight: 800;
}

.inline {
	display: inline !important;
}

.addIconTextElement {
	display: inline;
	padding-left: 5px;
	font-size: 17px;
}

.addIcon {
	top: 30%;
	width: 22px;
	height: 22px;
	background-color: #80c17d;
	border-radius: 50%;
	display: inline-flex;
	justify-content: center;
	align-items: center;
	color: #fff;
	font-size: 19px;
	cursor: pointer;
}
.addIconDisable {
	composes: addIcon;
	opacity: 0.7;
	pointer-events: none;
	cursor: pointer !important;
}
.addIcon:hover {
	background-color: #72ad70;
}

.cargoItemContainer {
	min-width: 250px;
}

.slideInButtonContainer {
	padding-top: 1rem;
}
