.table {
	width: 100%;
	background-color: rgba(0, 0, 0, 0.05);
	border-radius: 5px;
	padding: 10px;
	margin-bottom: 10px;
	color: #536476;
}

.leftAlign {
	text-align: left;
}

.rightAlign {
	text-align: right;
}

.centerAlign {
	text-align: center;
}

.table td,
.table th {
	padding-left: 3px;
	padding-right: 3px;
}
