.header {
	font-size: 28px;
	line-height: 28px;
}

.name {
	width: 100%;
	display: inline-block;
	margin-left: 1em;
}

.iconContainer {
	float: left;
	min-height: 4em;
}

.iconContainer > :global(.icon) {
	font-size: 35px !important;
}

.headerContainer {
	display: flex;
	align-items: center;
	justify-content: space-between;
	max-height: 132px;
	color: #536476;
	padding: 22px 20px;
}

.lockedHeaderContainer {
	background-color: #f5ecdb;
}

.search {
	font-size: 16px;
	padding-right: 2px;
	text-align: right;
}

.credit {
	margin: 2px 0;
}

.green {
	color: #80c17d;
}

.red {
	color: #db2828;
	font-weight: bold;
	padding: 0 1em 0 0;
}

.tabHeader {
	font-size: 28px;
	line-height: 28px;
}

.tabHeaderArea {
	margin-right: 5px;
	font-size: 16px;
	line-height: 1;
	text-align: right;
}

.headerEllipsis {
	float: left;
	margin: 0 6em 0 -2em;
}

.customerInformation {
	margin: 0 2rem 0 0;
}

.customerInformation:not(:first-child):before {
	content: '';
	position: absolute;
	left: -16px;
	top: 10px;
	display: block;
	width: 4px;
	height: 4px;
	border-radius: 50%;
	background-color: #6d7c8b;
}

.customerInfoBlock {
	display: flex;
	margin-top: 5px;
	font-size: 16px;
}

.buttonContainer {
	float: right;
}

.headerWithVideo h5 {
	display: inline-block;
	margin: 0;
	margin-right: 0.5em;
}

.headerWithVideo {
	position: relative;
	top: 50%;
	transform: translateY(-50%);
}

.link {
	color: #80c17d !important;
	font-size: 16px !important;
}

.addLoadLink {
	margin-left: 5px !important;
}

.customerDetails {
	color: #536476;
	font-weight: 600;
}

.customerCredit {
	color: #6d7c8b;
}

.secondaryInfoBlock {
	display: flex;
	margin-top: 8px;
	font-size: 16px;
}

.ellipsisIcon div > div > div {
	background-color: transparent !important;
}

.ellipsisIcon div > div > div {
	background-color: transparent !important;
}

.headerContainerFlex {
	display: flex;
	justify-content: space-between;
}

.headerTitlesFlex {
	display: flex;
	align-items: center;
}
