.marginRight15 {
	margin-right: 15px;
}

.floatLeft {
	float: left;
}

.floatRight {
	float: right;
}

.textOverflowEllipsis {
	/* all 3 of these values work together, per https://stackoverflow.com/a/17783233/5038805 */
	text-overflow: ellipsis;
	overflow: hidden;
	white-space: nowrap;
}

.maxWidth40Percent {
	max-width: 40%;
}

.lineHeight125Rem {
	line-height: 1.25rem;
}

.textAlignRight {
	text-align: right;
}

.textGreenColor {
	color: #80c17d;
}

.flexbox {
	display: flex;
	flex-direction: row;
	align-content: space-between;
	flex-wrap: nowrap;
	align-items: center;
}

.flexbox .flexCell {
	min-width: 0;
	align-self: center;
}

.flexCellEvenSpaced {
	flex-grow: 1;
	flex-shrink: 3;
}

.flexCellMinimumWidth {
	flex-basis: auto;
	flex-shrink: 1;
}

.marginTop5px {
	margin-top: 5px;
}

.marginRight5px {
	margin-right: 5px;
}

.displayInlineBlock {
	display: inline-block;
}
