.segments {
	border: 0 !important;
	box-shadow: none !important;
	border-radius: 0 !important;
}

.segmentsDisabled {
	opacity: 0.5;
}

.noVerticalMargin {
	margin-top: 0 !important;
	margin-bottom: 0 !important;
}

.segmentLabel {
	font-size: 16px;
	font-weight: 600;
	line-height: 1.19;
	color: #536476;
	cursor: pointer;
}

.segment {
	height: 50px;
	box-sizing: border-box;
	border: 2px solid #dde4eb !important;
	display: flex;
	align-items: center;
	justify-content: center;
	cursor: pointer;
}

.segment:first-child {
	border-top-left-radius: 6px !important;
	border-bottom-left-radius: 6px !important;
}

.segment:last-child {
	border-top-right-radius: 6px !important;
	border-bottom-right-radius: 6px !important;
}

.segment:not(:last-child) {
	border-right: 0 !important;
}

.segmentsEnabled .segment:hover {
	background-color: rgba(141, 200, 138, 0.9) !important;
}

.segmentsEnabled .segment:hover .segmentLabel {
	color: #ffffff;
}

.segmentSelected {
	background-color: #80c17d !important;
}

.segmentSelected .segmentLabel {
	color: #ffffff;
	font-weight: 600;
}

.optionsEqualWidth {
	width: 50% !important;
}
