.minimizedBuckets {
	max-height: calc(100vh - 370px);
}

@media (max-width: 1400px) {
	.minimizedBuckets {
		max-height: calc(100vh - 430px);
	}
}
@media (max-width: 1025px) {
	.minimizedBuckets {
		max-height: unset;
	}
}
.notMinimizedBuckets {
	max-height: calc(100vh - 510px);
}

@media (max-width: 1400px) {
	.notMinimizedBuckets {
		max-height: unset;
	}
}
