/* Customize the label (the container) */
.container {
	position: relative;
	padding-left: 0.8em;
	cursor: pointer;
	font-size: 18px;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
}

.flexContainer {
	display: flex;
	flex-direction: row;
	flex-wrap: nowrap;
	align-items: center;
}

/* On mouse-over, add a grey background color */
.container input ~ .checkmark,
.container:hover input ~ .checkmark,
.container input:focus ~ .checkmark {
	background-color: #e3e3e3;
}

/* When the radio button is checked, add a green background */
.container input:checked ~ .checkmark {
	background-color: #80c17d;
	border: solid 1px #cec9c9;
}

/* When the radio button is disabled, add an opacity */
.container input:checked:disabled ~ .checkmark {
	background-color: #80c17d;
	opacity: 0.5;
}

.container input:disabled ~ .checkmark {
	opacity: 0.5;
}

.container span {
	font-family: 'Source Sans Pro', sans-serif !important;
	letter-spacing: normal;
	color: #536476;
	line-height: normal;
}

/* When the radio button is disabled, add an opacity to appropriate Label */
.controlsDisabled {
	opacity: 0.5;
}

.toggleLabel:global(.ui.toggle.checkbox label) {
	font-size: 18px;
	padding-left: 3.5rem;
	color: #536476;
}

.container.noPaddingLeft {
	padding-left: 0;
}

/* When the radio button is toggle element */
:global(.ui.toggle.checkbox.checked input:checked ~ label:before) {
	background-color: #80c17d !important;
	border: none;
}

.defaultToggle:global(.ui.toggle.checkbox label:before) {
	width: 38px;
	height: 22px;
	border: solid 2px #cfcfcf;
	background-color: #e3e3e3;
}

.checkedToggle:global(.ui.toggle.checkbox input:checked ~ label:after) {
	left: 1.2rem;
	border: solid 2px #80c17d;
}

:global(.ui.toggle.checkbox input ~ label:after) {
	z-index: 1 !important;
	width: 1.5rem;
	height: 1.5rem;
	border: solid 2px #cfcfcf;
	background-color: #ffffff;
}

.pad {
	top: 15%;
}
