.deleteButton {
	composes: deleteConfirmationButton from '../input/CoreActionButton/DeleteConfirmationButton.module.css';
	vertical-align: top !important;
}
.tableEllipsisButton {
	left: 8px;
}

.tableEllipsisButton > :global(.icon:before) {
	content: url('./assets/green-ellipse-close.svg') !important;
}

.tableEllipsisButtonRotated {
	content: url('./assets/green-ellipse-close.svg') !important;
	left: 8px;
	transform: rotate(90deg);
}
