.message {
	font-size: 12px;
	font-weight: 600;
	letter-spacing: 0.25px;
	color: #e23239;
	white-space: pre-line;
}

.smallAlertIcon {
	vertical-align: sub;
	width: 1.4em !important;
	height: 1.2em !important;
	background-image: url('../CoreIcon/Assets/red-alert.svg') !important;
	background-position: center;
	background-size: cover;
}
.smallAlertIcon:before {
	content: none !important;
}
