.dottedInput {
	/*display: flex;*/
	/*align-items: center;*/
	font-family: 'Source Sans Pro';

	/*margin-left: 16px;*/
	/*margin-right: 16px;*/
	width: 151px;
	height: 48px;
	font-size: 40px;
	font-weight: 600;
	font-stretch: normal;
	font-style: normal;
	line-height: 1.38;
	letter-spacing: normal;
	text-align: center;
	color: #80c17d;
	border: none;
	background-image: url("data:image/svg+xml,%3csvg width='100%25' height='100%25' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='100%25' height='100%25' fill='none' stroke='%23C2C0C0FF' stroke-width='1' stroke-dasharray='6%2c 14' stroke-dashoffset='0' stroke-linecap='square'/%3e%3c/svg%3e");
	background-color: transparent;
	padding-bottom: 0;
	outline: none;
}

.dottedInput:focus {
	outline: none !important;
	border: 1px solid #00d2fa;
	border-radius: 2px;
	background-image: none;
	/*box-shadow: 0 0 10px #719ECE;*/
}
