.labeledButton.ui[class*='right labeled'].icon.button {
	padding-right: 3em !important;
	padding-left: 1.5em !important;
}

.labeledButton.ui[class*='right labeled'].icon.button > :global(.icon) {
	right: 0.5em !important;
	top: 0.35em !important;
	border-radius: 4px !important;
	width: 1.5em !important;
	height: 1.5em !important;
	background-color: transparent !important;
	margin-top: 4px !important;
}

.noIconOnButton.ui[class*='right labeled'].icon.button {
	padding-right: 1.5em !important;
	padding-left: 1.5em !important;
}

.noIconOnButton.ui[class*='right labeled'].icon.button > :global(.icon) {
	display: none !important;
}

.size {
	/*height: 44px !important;*/
	/*Commented, since some changes possible*/
	height: 50px;
	font-size: 20px !important;
}

.highlight-focus {
}

.highlight-focus:focus-visible {
	outline: 2px dotted black;
	outline-offset: -2px;
}
