.hide {
	display: none;
}

.carrierTable {
	max-height: 250px;
	overflow-y: auto;
	width: 100%;
}

.carrierTable > div {
	overflow-y: unset;
}

.verticalCenter {
	top: 50%;
	transform: translateY(-50%);
}

.floatRight {
	float: right;
}

.ediToggle {
	vertical-align: center;
	float: right;
	margin-right: 2em;
	margin-top: 1em;
}

.right {
	text-align: right;
}

.center {
	text-align: center;
}

.noPadding {
	composes: noPadding from '../../../../../core/components/input/CoreButton/CoreButton.module.css';
}

.spotPricing {
	float: right;
	top: 32%;
	color: #8bc68a;
	cursor: pointer;
	font-size: 15px;
}

.clearSearch {
	color: #8bc68a;
	cursor: pointer;
	font-size: 15px;
	padding-left: 1em;
}

.vendorSearchGrid {
	border-top: 1px solid rgba(34, 36, 38, 0.15);
}

.iconImage {
	padding-top: 10px;
}

.gridInfoTag {
	background-color: #a8b1ba;
	border-radius: 5px;
	height: 20px;
	width: 20px;
	display: flex;
	line-height: 20px; /*This needs to match height in order for the center to work */
	align-items: center;
	justify-content: center;
}

.gridInfoTagText {
	font-size: 12px;
	font-weight: bold;
	color: #ffffff;
	font-stretch: normal;
	letter-spacing: normal;
}

.carrierRecRedTag {
	padding: 2px 5px 2px 5px;
	color: #fa5680;
	font-weight: bold;
}

.carrierRecGreenTag {
	color: #80c17d;
	padding: 2px 5px 2px 5px;
	font-weight: bold;
}
