.headerContainer {
	display: flex;
	align-items: center;
	justify-content: space-between;
	max-height: 132px;
	color: #536476;
	padding: 22px 20px;
}

.lockedHeaderContainer {
	background-color: #f5ecdb;
}

.tabHeader {
	font-size: 28px;
	line-height: 28px;
}

.customerInfoBlock {
	display: flex;
	margin-top: 5px;
	font-size: 16px;
}

.customerInformation {
	margin: 0 2rem 0 0;
}

.customerInformation:not(:first-child):before {
	content: '';
	position: absolute;
	left: -16px;
	top: 10px;
	display: block;
	width: 4px;
	height: 4px;
	border-radius: 50%;
	background-color: #6d7c8b;
}

.link {
	color: #80c17d !important;
	font-size: 16px !important;
}

.credit {
	margin: 2px 0;
}

.tabHeaderArea {
	margin-right: 5px;
	font-size: 16px;
	line-height: 1;
	text-align: right;
}

.customerDetails {
	color: #536476;
	font-weight: 600;
}

.red {
	color: #db2828;
	font-weight: bold;
	padding: 0 1em 0 0;
}

.ellipsisActionContainer {
	float: right;
	margin-top: -1.2em;
	margin-right: -1.5em;
}
