.buttonRow {
	display: flex !important;
	flex-direction: row;
	justify-content: space-between;
	flex-wrap: nowrap;
	padding-top: 5px;
}

.buttonRow > :first-child {
	margin-right: 10px;
}

.buttonRow > :not(:first-child):not(:last-child) {
	margin-left: 10px;
	margin-right: 10px;
}

.buttonRow > :last-child {
	margin-left: 10px;
}
