:global a.ui.label {
	margin: 1px !important;
	padding: 4px !important;
}

.middleAlign {
	width: 100%;
	height: 100%;
	padding-top: 11px;
	padding-left: 5px;
}

.btnsWrapper {
	display: flex;
	justify-content: space-between;
	margin: 1rem 0 2rem;
}

.clearBtn {
	font-size: 18px !important;
	color: #80c17d !important;
	background-color: transparent !important;
	padding-right: 0 !important;
	padding-left: 0 !important;
}

.clearBtn:hover,
.clearBtn:focus {
	opacity: 0.8;
}
