@value colors: '../../../../common/common.module.css';
@value labelsColor from colors;

.shipmentsVideoButton {
	position: absolute;
	bottom: 0;
	right: 0;
	transform: translateY(-10%);
}
.loadFilterContainer {
	top: 2em;
	display: flex;
	align-items: flex-start;
	justify-content: flex-end;
	padding-right: 1em;
}

.refreshing {
	color: labelsColor;
}

.searchToggles {
	margin-left: auto;
	display: flex;
	justify-content: flex-end;
}

.container {
	display: flex;
	flex-wrap: wrap-reverse;
}

.quickTasksContainer {
	margin-bottom: 20px;
	width: 100%;
}

.gridContainer {
	min-width: 100%;
}

.shipmentBucketsMinimized {
	display: flex;
	justify-content: space-between;
}

.shipmentBucketsActions {
	display: flex;
	align-items: center;
	/* TODO move to const */
	height: 50px;
}

@media (min-width: 1350px) {
	.quickTasksContainer {
		margin-bottom: 0;
		margin-left: 20px;
		width: auto;
	}

	.headerContainer {
		width: auto;
	}

	.container {
		flex-wrap: initial;
	}

	.gridContainer {
		min-width: auto;
	}
}

.headerContainer {
	width: 80%;
}

.helpButtonMinimized {
	margin-left: 0.5rem;
}
