.buttonContainer {
	float: right;
}

.mileageContainer {
	margin-top: 10px !important;
	float: right;
	padding-right: 10px;
}

.error {
	margin-top: 10px !important;
	margin-bottom: 10px !important;
	color: red;
}

.insideSidebarContainer {
	background-color: transparent !important;
	border: none !important;
	border-radius: 0;
	box-shadow: none !important;
	padding: 0 !important;
}

.insideSidebarContainer hr {
	display: none;
}

.engineBlock {
	margin: 15px 0;
}

.mileageContainerSidebar {
	margin-top: 25px !important;
	display: flex;
	align-items: center;
	justify-content: center;
	height: 60px;
	font-size: 30px;
	font-stretch: normal;
	font-style: normal;
	line-height: 0.83;
	letter-spacing: normal;
	color: #ffffff;
	border-radius: 9px;
	background-color: #467599;
	width: 100%;
}

.mileageContainerSidebar > div {
	text-align: center;
}

.mileageTotal {
	text-transform: uppercase;
	margin-bottom: 3px;
	font-size: 16px;
}

.clear {
	top: 32%;
	color: #8bc68a;
	cursor: pointer;
	font-size: 15px;
}

.directionLabel {
	font-size: 16px !important;
	line-height: 1.19 !important;
	color: #6d7c8b !important;
	right: 10px;
}
