@value colors: '../../../../common/common.module.css';
@value primaryColor, labelsColor from colors;

.buttons {
	margin-top: 1em;
}

.headerLink {
	color: primaryColor !important;
	margin-left: 0.25em;
}

.headerLink:hover {
	color: primaryColor !important;
	text-decoration: underline;
}

.primaryInfo {
	font-size: 28px;
	margin: 0em;
}

.secondaryInformation {
	margin: 0 2rem 0 0;
}

.secondaryInformation:not(:first-child):before {
	content: '';
	position: absolute;
	left: -16px;
	top: 10px;
	display: block;
	width: 4px;
	height: 4px;
	border-radius: 50%;
	background-color: #6d7c8b;
}

.secondaryInfoBlock {
	display: flex;
	margin-top: 5px;
	font-size: 16px;
}

.active {
	color: #21ba45;
}
.inactive {
	border-color: #fa5680;
	color: #fa5680;
}

.inactive,
.active {
	font-size: 18px;
	border-width: 0.25em;
	display: inline-block;
	margin-left: 1em;
}
