@value colors: './../../../common/common.module.css';
@value primaryColor, labelsColor from colors;

.container {
	display: inline-block;
	cursor: pointer;
	margin: 0 0.4rem;
	width: auto;
	min-width: 9rem;
	min-height: 120px;
	border-radius: 4px;
	padding: 1.7rem 0.5rem 1.2em;
	color: labelsColor;
}

.container:hover {
	background-color: #f3f6f8;
}

.containerActive {
	composes: container;
	background-color: primaryColor;
	color: white;
}

.containerActive:hover {
	background-color: primaryColor;
}

.containerMinimized {
	min-height: 50px;
	height: 50px;
	width: auto;
	padding: 8px;
	display: inline-flex;
	flex-flow: column;
	margin: 0 5px;
}

.headersContainer {
	position: relative;
	bottom: 10px;
}

.headersContainerMinimized {
	position: static;
}

.count {
	display: table;
	margin: 0 !important;
	padding: 1.4rem 10px 0.3rem 0;
	line-height: 2.5rem;
	font-weight: bolder;
	font-size: 34px;
}

.containerActive .countMinimized {
	color: #fff;
}

.countMinimized {
	font-size: 24px;
	font-weight: 600;
	line-height: 22px;
	color: #536476;
	padding: 0;
	display: block;
	position: relative;
	top: -2px;
}

.loader {
	margin-top: 1.4rem;
	margin-left: -1.3rem;
	background-color: rgb(0, 0, 0, 0) !important;
}

.loader.loaderMinimized {
	margin: -16px 0 0;
}

.loader > :global(.ui.active.transition.visible.inverted.dimmer) {
	background-color: rgb(0, 0, 0, 0) !important;
}

.container:not(:last-child):after {
	content: '';
	width: 1px;
	height: 80px;
	position: absolute;
	right: -6px;
	top: 1.35em;
	opacity: 0.15;
	background-color: #000000;
}

.containerMinimized:not(:last-child):after {
	width: 2px;
	height: 42px;
	right: -6px;
	top: 4px;
	opacity: 1;
	background-color: #ededed;
}

.header {
	text-transform: uppercase;
	font-size: 1rem;
	font-weight: bold;
	line-height: 18px;
}

.subHeader {
	display: block;
	margin-top: 4px;
	opacity: 0.7;
	font-family: 'Source Sans Pro', sans-serif !important;
	font-size: 12px;
	line-height: 1;
	letter-spacing: normal;
	color: #bababa;
}
.subHeaderActive {
	composes: subHeader;
	opacity: 1;
	color: #ffffff;
}

.headerActive {
	composes: header;
	background-color: primaryColor;
	color: white;
	border-radius: 0.28571429rem;
}

.filterText {
	color: lightgray;
	margin-bottom: 0.5rem;
	line-height: 1rem;
	font-size: 0.9rem;
}

.filterTextActive {
	composes: filterText;
	color: white;
}

.centered {
	position: relative;
	left: 30%;
	top: 2.5em;
}

.loadGridFilter {
	padding-top: 0.2em;
	padding-bottom: 0.2em;
	bottom: 0.3em;
}
