@value colors: '../../../common/common.module.css';
@value primaryColor, labelsColor from colors;

.buttons {
	float: right;
	margin-top: 1em;
}

.name {
	display: inline-block;
	margin-left: 1em;
}

.name > h3 {
	font-weight: bold;
	margin-bottom: 0.3em;
}
.name > h4 {
	margin-top: 0;
	margin-bottom: 0;
}
.name > h5 {
	margin-top: 0.5em;
}

.pendingCarrierSearchPadding {
	padding-bottom: 2em;
}

.iconContainer {
	float: left;
	min-height: 4em;
	padding-top: 0.5em;
	padding-left: 0.5em;
}

.headerLink {
	color: primaryColor !important;
	margin-left: 0.25em;
}

.headerLink:hover {
	color: primaryColor !important;
	text-decoration: underline;
}

.primaryInfo {
	font-size: 28px;
	margin: 0em;
}
