.noneSpacing {
	margin: 0px !important;
	padding: 0px !important;
}

.noneSpacing > :global(.row) {
	padding-top: 0px !important;
	padding-bottom: 0px !important;
}

.noneSpacing > :global(.row) > :global(.column) {
	margin: 0px !important;
	padding: 0px !important;
}

.tinySpacing {
	margin: -0.2rem -0.2rem 0.2rem !important;
}

.tinySpacing:last-child {
	margin: -0.2rem !important;
}

.tinySpacing > :global(.row) {
	padding-top: 0.2rem !important;
	padding-bottom: 0.2rem !important;
}

.tinySpacing > :global(.row) > :global(.column) {
	padding-left: 0.2rem !important;
	padding-right: 0.2rem !important;
}

.normalSpacing {
	margin: -0.7rem -0.7rem 0.7rem !important;
}

.normalSpacing:last-child {
	margin: -0.7rem !important;
}

.normalSpacing > :global(.row) {
	padding-top: 0.7rem !important;
	padding-bottom: 0.7rem !important;
}

.normalSpacing > :global(.row) > :global(.column) {
	padding-left: 0.7rem !important;
	padding-right: 0.7rem !important;
}

.grey {
	background-color: #f1f1f1;
}
