.checkboxSize {
	height: 50px;
	margin: 0;
	border: 2px solid #dde4eb !important;
}

.redCheckBox :global(.ui.toggle.checkbox.checked input:checked ~ label:before) {
	background-color: red !important;
}

.carrierNameField {
	position: relative;
	width: 100%;
}

.unlockButton {
	position: absolute;
	top: 10px;
	right: 10px;
	float: right;
	width: 24px;
	height: 24px;
	z-index: 1;
}
