.widget {
	text-align: center;
	min-width: 360px;
	min-height: 600px;
}

.noBorder {
	border: none !important;
	box-shadow: none !important;
}

.introduction {
	margin: 16px 30px 16px 30px;
}

.circles {
	height: 250px;
	margin-bottom: 14px;
}

.popup .circles {
	margin-left: 50px;
}

.textBubble {
	width: 100%;
	border-radius: 9px;
	border: solid 1px #dde4eb;
	background-color: #f1f1f1;
	color: #98a2ac;
	text-align: left;
	padding: 1px 15px 1px 20px;
	box-shadow: 0 1px 10px 0 rgba(0, 0, 0, 0.2);
}

.notificationsCircle {
	position: relative;
	width: 100%;
}

.notificationsCircle .textBubble {
	position: relative;
	width: 200px;
	left: 1%;
	top: -120px;
}

.actionsCircle {
	width: 188px;
	position: relative;
	top: -135px;
	left: 60%;
}

.actionsCircle .textBubble {
	position: relative;
	width: 212px;
	left: -40%;
	top: -130px;
}

.generalCircle {
	width: 188px;
	position: relative;
	top: -240px;
	left: 40%;
}

.generalCircle .textBubble {
	position: relative;
	top: -138px;
	left: -20%;
}

.qbitsText {
	font-family: 'Source Sans Pro', sans-serif;
	text-align: center;
	color: #536476;
	font-size: 16px;
	margin-bottom: 16px;
}
