.avatarButtonWrapper {
	display: inline-block;
	vertical-align: middle;
}

.avatarButton:global(.ui.dropdown) {
	display: flex;
	align-items: center;
	justify-content: center;
	width: 45px;
	height: 45px;
	border-radius: 50%;
	background: #ddd;
}

.avatarButton :global .text {
	position: absolute;
	z-index: 3;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -55%);
	font-weight: bold;
	font-size: 18px;
	color: #fff;
	pointer-events: none;
}

.avatarCircle {
	position: absolute;
	z-index: 2;
	width: 39px;
	height: 39px;
	border-radius: 50%;
	background-color: #c1c1c1;
	border: 1px solid #fff;
	transition: background-color 0.2s ease;
}

.avatarCircle:hover,
.avatarButton:global(.active) > .avatarCircle {
	background-color: #a6a6a6;
}
