.labeledButton.ui.icon.squareButton {
	padding-right: 3em !important;
	padding-left: 1.5em !important;
}

.labeledButton.ui.icon.squareButton > :global(.icon) {
	bottom: 24px;
	border-radius: 4px !important;
	width: 1.5em !important;
	height: 1.5em !important;
	background-color: rgba(0, 0, 0, 0.2) !important;
	margin-top: 5px !important;
	left: 72px;
	padding: 4px;
}

.deleteStyledButton {
	composes: deleteConfirmationButton from './DeleteConfirmationButton.module.css';
	padding: 0 !important;
}

.customIconWrapper {
	display: inline-block;
	vertical-align: top;
}

.tertiaryMenuActiveBoarder :global(.menu.visible) {
	border-color: #467599 !important;
}

.size {
	height: 44px;
	width: 124.47px;
}

.squareButton {
	max-width: 124.47px;
	bottom: 1px;
}

.actionsMenu {
	top: unset !important;
	bottom: auto !important;
}

.selectionItem:hover {
	cursor: pointer;
	background-color: #f1f1f1;
}

.selectionItem > :global(.icon) {
	margin-right: 1rem;
}

/* Sort of hacky way to get the button to have a right margin like other buttons */
.rightMargin:not(:last-child) {
	font-size: inherit !important;
	margin-right: 0 !important;
}
.rightMargin:not(:last-child):after {
	width: 0;
}

.selectionItem > :global(.icon.video.play) {
	color: #21ba45;
}

.selectionItem > :global(.icon.file.pdf) {
	color: red;
}

.selectionItem > :global(.icon.file.word) {
	color: #0082be;
}

.selectionItem > :global(.icon.file.excel) {
	color: #008000;
}

.popupDropDown {
	border-radius: 6px !important;
	box-shadow: 0 4px 13px 0 #cccccc !important;
	border: solid 1px #dde4eb !important;
	background-color: #ffffff !important;
	padding: 12px 0 !important;
	margin-top: 0 !important;
	min-width: 180px !important;
}

.popupDropDown:global(.ui.popup > .header) {
	padding: 0 20px;
}

.dropDownItems {
	color: #536476;
	font-size: 14px;
	font-weight: normal;
	font-stretch: normal;
	font-style: normal;
	line-height: 1.36;
	letter-spacing: normal;
	font-family: 'Source Sans Pro', sans-serif !important;
	padding: 6px 0;
}

.dropDownItems:hover {
	background-color: #ecf2f6;
}

.dropDownItems span {
	padding: 0 20px;
}

/*Reset form button*/
.clearPosition {
	padding: 8px 7px !important;
}
.clearPosition i {
	margin: 0 !important;
}

/* Confirmation Popup start */
.confirmationButtonWrapper {
	display: inline-flex;
	height: 50px;
	align-items: center;
	justify-content: center;
	font-weight: 600;
	font-size: 18px;
}
.confirmationButtonWrapper > :global(.dropdown) {
	color: #80c17d;
	transition: all 0.2s;
}
.confirmationButtonWrapper > :global(.ui.dropdown:hover),
.confirmationButtonWrapper > :global(.ui.dropdown:focus) {
	opacity: 0.8;
}
.confirmationPopup {
	composes: popupDropDown;
	max-width: 400px !important;
	padding: 20px !important;
	border: none !important;
	box-shadow: 0 4px 13px 0 rgba(0, 0, 0, 0.15) !important;
}
.confirmationPopup :global .content {
	display: flex;
	justify-content: flex-end;
}
.confirmationPopup :global .header {
	margin: 5px 0 20px;
	font-size: 18px !important;
	font-weight: normal !important;
	font-family: 'Source Sans Pro', sans-serif !important;
	color: #5d6f82;
}
.primaryPopupButton {
	composes: primaryButton from '../CoreButton/CoreButton.module.css';
	margin-left: 20px;
}
.secondaryPopupButton {
	composes: secondaryButton from '../CoreButton/CoreButton.module.css';
}
.tertiaryPopupButton {
	composes: tertiaryButton from '../CoreButton/CoreButton.module.css';
	font-size: 20px !important;
}
.primaryPopupButton,
.secondaryPopupButton,
.tertiaryPopupButton {
	display: inline-flex;
	align-items: center;
	justify-content: center;
	min-width: 130px;
	font-size: 20px;
	border-radius: 6px;
}
/* Confirmation Popup end */
