.title {
	font-weight: bold;
}

.capitalized {
	text-transform: capitalize;
}

.eventMargin,
.loadEvents {
	margin: 0;
	padding: 8px 0;
}

.loadEvents {
	font-size: 15px;
	line-height: 19px;
	white-space: initial;
	color: #5e6f81;
	border: none;
	border-radius: 0;
	background-color: #fff;
	box-shadow: none;
}

.loadEvents:not(:first-child) {
	border-top: 1px solid #e3e3e3;
}
